import React from "react";
import style from "./StyledCard.module.css";

const ProductCard = ({ product, showDiscountBadge = true }) => {
  const hasDiscount = Number(product.discount) > 0;
  const formatoMoneda = new Intl.NumberFormat("es-AR", { 
    style: "currency", 
    maximumFractionDigits: 0,
    currency: "ARS" 
  });

  return (
    <li className={style.productCard}>
      <a href={`/products/${product.sku}`} className={style.productLink}>
        <div className={style.imageContainer}>
          <img src={product.image} alt={product.name} className={style.productImage} />
          {showDiscountBadge && hasDiscount && (
            <span className={style.discountBadge}>
              -{product.discount}%
            </span>
          )}
        </div>
        <article className={style.productInfo}>
          <h2 className={style.productName}>{product.name}</h2>
          <div className={style.priceContainer}>
            <span className={`${style.originalPrice} ${hasDiscount ? style.strikethrough : ''}`}>
              {formatoMoneda.format(product.price)}
            </span>
            {hasDiscount && (
              <span className={style.discountedPrice}>
                {formatoMoneda.format(product.price * (1 - Number(product.discount) / 100))}
              </span>
            )}
          </div>
        </article>
      </a>
    </li>
  );
};

export default ProductCard;