import { useState, useEffect } from "react";
import useProducts from "../../context/useProducts";
import styles from "./ProductForm.module.css";
import { useRef } from "react";
function useDebouncedCallback(callback, delay) {
  const timeoutRef = useRef();
  return (...args) => {
    clearTimeout(timeoutRef.current);
    timeoutRef.current = setTimeout(() => callback(...args), delay);
  };
}

function brandCode(category) {
  const map = { Adidas: "ADI", Nike: "NK", Vans: "VNS", LV: "LV", "Luis Vuitton": "LV", DC: "DC", Puma: "PM" };
  return map[category] || String(category || "").toUpperCase().slice(0, 3);
}

function modelCode(name = "", category = "") {
  let model = String(name);
  const brand = String(category);
  if (model.toLowerCase().startsWith(brand.toLowerCase())) model = model.slice(brand.length).trim();
  const tokens = model.split(/\s+/).filter(Boolean);
  const letters = tokens.filter(t => /[A-Za-z]/.test(t)).map(t => t[0].toUpperCase());
  const digits = tokens.map(t => (t.match(/\d+/) || [""])[0]).find(Boolean);
  let code = (letters[0] || "");
  if (letters.length > 1) code += letters[1];
  if (digits) code += digits;
  return (code.slice(0,3) || (tokens[0] ? tokens[0].slice(0,3).toUpperCase() : "MDL"));
}

function parseSizeValue(size) {
  const n = Number(String(size).replace(/[^0-9.]/g, ""));
  return isNaN(n) ? Infinity : n;
}

const ProductForm = ({ product = null, onClose, mode = "create" }) => {
  const { createProduct, updateProduct, loading, error, clearError, getProductBySku } = useProducts();
  
  const [formData, setFormData] = useState({
    name: "",
    sku: "",
    price: "",
    stock: 0,
    talle: [],
    sizes: [],
    category: "",
    image: "",
    discount: 0,
    description: ""
  });

  const [sizeInput, setSizeInput] = useState("");
  const [sizeQtyInput, setSizeQtyInput] = useState("");
  const [skuPreview, setSkuPreview] = useState("");
    useEffect(() => {
    const fetchFull = async () => {
      if (mode === "edit" && product?.sku) {
        try {
          const full = await getProductBySku(product.sku);
          if (full) {
                        const sizes = (full.sizes && full.sizes.length)
              ? full.sizes.map(s => ({ ...s })).sort((a, b) => parseSizeValue(a.size) - parseSizeValue(b.size) || String(a.size).localeCompare(String(b.size)))
              : (full.talle || []).slice().sort((a, b) => parseSizeValue(a) - parseSizeValue(b)).map(s => ({ size: s, quantity: 0 }));
            const stock = sizes.reduce((acc, s) => acc + (Number(s.quantity) || 0), 0);
            setFormData({
              name: full.name || "",
              sku: full.sku || "",
              price: full.price || "",
              stock,
              talle: sizes.map(s => s.size),
              sizes,
              category: full.category || "",
              image: full.image || "",
              discount: full.discount || 0,
              description: full.description || ""
            });
          }
        } catch (e) {
        }
      }
    };
    fetchFull();
  }, [mode, product?.sku, getProductBySku]);
  
  const recomputeSku = async (name, category) => {
    if (!name || !category) { setSkuPreview(""); return; }
    try {
      const urlBackend = import.meta.env.VITE_BACKEND_URL || "http://localhost:4000";
      const res = await fetch(`${urlBackend}/api/products`);
      const data = await res.json();
      const countBrand = data.filter(p => p.category === category).length;
      const seq = String(countBrand + 1).padStart(3, "0");
      const sku = `${brandCode(category)}-${modelCode(name, category)}-${seq}`;
      setSkuPreview(sku);
    } catch (_) {
      setSkuPreview("");
    }
  };

  const debouncedSku = useDebouncedCallback((n, c) => recomputeSku(n, c), 400);
  useEffect(() => {
    debouncedSku(formData.name, formData.category);
  }, [formData.name, formData.category]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const addSize = () => {
    const size = sizeInput.trim();
    const qty = Number(sizeQtyInput);
    if (!size) return;
    if (isNaN(qty) || qty < 0) return;
    if (formData.sizes.find(s => s.size === size)) return;
        setFormData(prev => {
      const newSizes = [...prev.sizes, { size, quantity: qty }]
        .sort((a, b) => parseSizeValue(a.size) - parseSizeValue(b.size) || String(a.size).localeCompare(String(b.size)));
      const stock = newSizes.reduce((acc, s) => acc + (Number(s.quantity) || 0), 0);
      return { ...prev, sizes: newSizes, stock, talle: newSizes.map(s => s.size) };
    });
    setSizeInput("");
    setSizeQtyInput("");
  };

  const updateSizeQty = (index, qty) => {
    const quantity = Math.max(0, Number(qty) || 0);
        setFormData(prev => {
      const newSizes = prev.sizes.map((s, i) => i === index ? { ...s, quantity } : s);
      const stock = newSizes.reduce((acc, s) => acc + (Number(s.quantity) || 0), 0);
      return { ...prev, sizes: newSizes, stock, talle: newSizes.map(s => s.size) };
    });
  };

  const removeSize = (index) => {
        setFormData(prev => {
      const newSizes = prev.sizes.filter((_, i) => i !== index);
      const stock = newSizes.reduce((acc, s) => acc + (Number(s.quantity) || 0), 0);
      return { ...prev, sizes: newSizes, stock, talle: newSizes.map(s => s.size) };
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    clearError();

    try {
      const productData = {
        ...formData,
        price: parseFloat(formData.price),
        stock: Number(formData.stock) || 0,
        discount: parseFloat(formData.discount) || 0,
      };

      if (mode === "create") {
        await createProduct(productData);
      } else {
        await updateProduct(productData.sku, productData);
      }
      
      onClose();
    } catch (err) {
      console.error("Error al guardar producto:", err);
    }
  };

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div className={styles.modalOverlay} onClick={handleOverlayClick}>
      <div className={styles.modal}>
        <div className={styles.modalHeader}>
          <h2>{mode === "create" ? "Crear Producto" : "Editar Producto"}</h2>
          <button onClick={onClose} className={styles.closeButton}></button>
        </div>

        {error && (
          <div className={styles.error}>
            {error}
            <button onClick={clearError}></button>
          </div>
        )}

        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.formGroup}>
            <label htmlFor="name">Nombre *</label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleInputChange}
              required
            />
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="sku">SKU {mode === 'create' ? '(se genera automticamente)' : ''}</label>
            <input
              type="text"
              id="sku"
              name="sku"
              value={formData.sku}
              onChange={handleInputChange}
              disabled
            />
            {mode === 'create' && skuPreview && (
              <small style={{ color: '#6b7280' }}>Sugerido: {skuPreview}</small>
            )}
          </div>

          <div className={styles.formRow}>
            <div className={styles.formGroup}>
              <label htmlFor="price">Precio *</label>
              <input
                type="number"
                id="price"
                name="price"
                value={formData.price}
                onChange={handleInputChange}
                step="0.01"
                min="0"
                required
              />
            </div>

            <div className={styles.formGroup}>
              <label htmlFor="stock">Stock total</label>
              <input
                type="number"
                id="stock"
                name="stock"
                value={formData.stock}
                onChange={handleInputChange}
                min="0"
                disabled
              />
            </div>
          </div>

          <div className={styles.formRow}>
            <div className={styles.formGroup}>
              <label htmlFor="category">Categora *</label>
              <select
                id="category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                required
              >
                <option value="">Seleccionar categora</option>
                <option value="Nike">Nike</option>
                <option value="Adidas">Adidas</option>
                <option value="Vans">Vans</option>
                <option value="LV">LV</option>
                <option value="DC">DC</option>
                <option value="Puma">Puma</option>
              </select>
            </div>

            <div className={styles.formGroup}>
              <label htmlFor="discount">Descuento (%)</label>
              <input
                type="number"
                id="discount"
                name="discount"
                value={formData.discount}
                onChange={handleInputChange}
                min="0"
                max="100"
              />
            </div>
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="image">URL de imagen</label>
            <input
              type="url"
              id="image"
              name="image"
              value={formData.image}
              onChange={handleInputChange}
              placeholder="..."
            />
          </div>

          <div className={styles.formGroup}>
            <label>Stock por talle</label>
            <div className={styles.talleInput}>
              <input
                type="text"
                value={sizeInput}
                onChange={(e) => setSizeInput(e.target.value)}
                placeholder="Ej: 39, 40, 41"
              />
              <input
                type="number"
                value={sizeQtyInput}
                onChange={(e) => setSizeQtyInput(e.target.value)}
                placeholder="Cant."
                min="0"
                style={{ width: 100 }}
              />
              <button type="button" onClick={addSize} className={styles.addButton}>
                +
              </button>
            </div>
            <div className={styles.talleList}>
              {(formData.sizes || []).map((s, index) => (
                <div key={`${s.size}-${index}`} className={styles.sizeRow}>
                  <span className={styles.sizeTag}>{s.size}</span>
                  <input
                    type="number"
                    min="0"
                    value={s.quantity}
                    onChange={(e) => updateSizeQty(index, e.target.value)}
                    className={styles.sizeQty}
                  />
                  <button
                    type="button"
                    onClick={() => removeSize(index)}
                    className={styles.removeTalle}
                  >
                    
                  </button>
                </div>
              ))}
            </div>
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="description">Descripcin</label>
            <textarea
              id="description"
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              rows="3"
            />
          </div>

          <div className={styles.formActions}>
            <button type="button" onClick={onClose} className={styles.cancelButton}>
              Cancelar
            </button>
            <button type="submit" className={styles.submitButton} disabled={loading}>
              {loading ? "Guardando..." : (mode === "create" ? "Crear" : "Actualizar")}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ProductForm; 