import React, { useState, useEffect, useRef } from "react";
import useProducts from "../../../../context/useProducts";
import ProductCard from "../../../Products/components/Card/ProductCard.jsx";
import style from "./StyledOffers.module.css";
import { motion, useInView, useAnimation } from "framer-motion";

const Offers = () => {
  const { list, loading } = useProducts();
  const offers = list.filter(product => product.discount && product.discount > 0);
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, amount: 0.1 });
  const [isVisible, setIsVisible] = useState(false);
  const controls = useAnimation();

  useEffect(() => {
    setIsVisible(true);
    
    if (isInView) {
      controls.start("visible");
    } else {
      if (offers.length > 0) {
        controls.start("visible");
      }
    }
  }, [isInView, controls, offers.length]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        when: "beforeChildren",
        staggerChildren: 0.1,
        delayChildren: 0.1,
        duration: 0.5
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: (i) => ({
      y: 0,
      opacity: 1,
      transition: {
        delay: 0.1 * i,
        duration: 0.5,
        ease: "easeOut"
      }
    })
  };

  if (loading) {
    return (
      <section id={style.offersSection} ref={ref}>
        <div className={style.loading}>
          Cargando ofertas...
        </div>
      </section>
    );
  }

  if (offers.length === 0) {
    return (
      <section id={style.offersSection} ref={ref}>
        <div className={style.noOffers}>
          No hay ofertas disponibles en este momento.
          <motion.p 
            className={style.subtitle}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.1 }}
          >
            No hay ofertas disponibles en este momento
          </motion.p>
        </div>
      </section>
    );
  }

  return (
    <section id="offersSection" className={style.offersSection} ref={ref} style={{ opacity: isVisible ? 1 : 0 }}>
      <div className={style.headerContainer}>
        <motion.h2 
          className={style.offersTitle}
          initial={{ opacity: 0, y: 20 }}
          animate={controls}
          variants={{
            hidden: { opacity: 0, y: 20 },
            visible: { opacity: 1, y: 0, transition: { duration: 0.6 } }
          }}
        >
          Ofertas Especiales
        </motion.h2>
        <motion.p 
          className={style.subtitle}
          initial={{ opacity: 0, y: 20 }}
          animate={controls}
          variants={{
            hidden: { opacity: 0, y: 20 },
            visible: { 
              opacity: 1, 
              y: 0, 
              transition: { 
                duration: 0.6,
                delay: 0.1 
              } 
            }
          }}
        >
          Aprovech nuestros descuentos especiales por tiempo limitado
        </motion.p>
      </div>

      <motion.ul 
        className={style.offersList}
        variants={containerVariants}
        initial="hidden"
        animate={controls}
      >
        {offers.map((offer, index) => (
          <motion.li 
            key={offer._id}
            custom={index}
            variants={itemVariants}
            whileHover={{ scale: 1.02 }}
            style={{ listStyle: 'none' }}
          >
            <ProductCard product={offer} showDiscountBadge={true} />
          </motion.li>
        ))}
      </motion.ul>
    </section>
  );
};

export default Offers;