import React from 'react';
import { jsPDF } from 'jspdf';
import autoTable from 'jspdf-autotable';
import styles from './InvoiceDetailModal.module.css';

const InvoiceDetailModal = ({ invoice, onClose }) => {
  if (!invoice) return null;

  const totalItems = invoice.items.reduce((acc, item) => acc + item.quantity, 0);

  const generatePDF = () => {
    const doc = new jsPDF();

    doc.setFontSize(24);
    doc.setTextColor(41, 128, 185);
    doc.setFont('helvetica', 'bold');
    doc.text('Shoeroom', 14, 20);
    
    doc.setDrawColor(200, 200, 200);
    doc.line(14, 25, 196, 25);
    
    doc.setFontSize(18);
    doc.setTextColor(0, 0, 0);
    doc.text('Detalle de Factura', 14, 40);
    
    doc.setFontSize(12);
    doc.text(`N Factura: ${invoice.invoiceNumber}`, 14, 50);
    doc.text(`Fecha: ${new Date(invoice.createdAt).toLocaleDateString('es-AR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })}`, 14, 56);

    const tableColumn = ["Producto", "Talle", "Cantidad", "Precio Unit.", "Subtotal"];
    const tableRows = [];

    invoice.items.forEach(item => {
      const productName = item.product?.name || 'Producto no disponible';
      const productSku = item.product?.sku || '';
      
      tableRows.push([
        `${productName}${productSku ? ` (${productSku})` : ''}`,
        item.size || '-',
        item.quantity || 0,
        `$${(item.price || 0).toFixed(2)}`,
        `$${((item.price || 0) * (item.quantity || 0)).toFixed(2)}`
      ]);
    });

    autoTable(doc, {
      head: [tableColumn],
      body: tableRows,
      startY: 65,
      styles: { fontSize: 10 },
      headStyles: { fillColor: [41, 128, 185] }
    });

    const finalY = doc.lastAutoTable.finalY || 70;
    doc.setFontSize(14);
    doc.setFont('helvetica', 'bold');
    doc.text(`Total: $${(invoice.totalAmount || 0).toFixed(2)}`, 14, finalY + 10);
    
    doc.setFontSize(10);
    doc.setFont('helvetica', 'normal');
    doc.setTextColor(100, 100, 100);
    doc.text('Gracias por su compra en Shoeroom!', 14, finalY + 25);
    doc.text('Para consultas, contacte a: contacto@shoeroom.com', 14, finalY + 30);

    doc.save(`Factura-${invoice.invoiceNumber}.pdf`);
  };

  return (
    <div className={styles.modalOverlay} onClick={onClose}>
      <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
        <div className={styles.modalHeader}>
          <h2>Detalle de Factura</h2>
          <button className={styles.closeButton} onClick={onClose} aria-label="Cerrar">
            <span></span>
          </button>
        </div>
        
        <div className={styles.invoiceInfo}>
          <div className={styles.infoRow}>
            <div className={styles.infoGroup}>
              <span className={styles.infoLabel}>N Factura:</span>
              <span className={styles.infoValue}>{invoice.invoiceNumber}</span>
            </div>
            <div className={styles.infoGroup}>
              <span className={styles.infoLabel}>Fecha:</span>
              <span className={styles.infoValue}>
                {new Date(invoice.createdAt).toLocaleDateString('es-AR', {
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric',
                  hour: '2-digit',
                  minute: '2-digit'
                })}
              </span>
            </div>
          </div>
        </div>
        
        <div className={styles.itemsContainer}>
          <div className={styles.tableHeader}>
            <div className={styles.tableRow}>
              <div className={`${styles.tableCell} ${styles.productCell}`}>Producto</div>
              <div className={styles.tableCell}>Talle</div>
              <div className={styles.tableCell}>Cantidad</div>
              <div className={styles.tableCell}>Precio Unit.</div>
              <div className={styles.tableCell}>Subtotal</div>
            </div>
          </div>
          
          <div className={styles.tableBody}>
            {invoice.items.map((item, index) => {
              const productName = item.product?.name || 'Producto no disponible';
              const productSku = item.product?.sku || '';
              const subtotal = (item.price || 0) * (item.quantity || 0);
              
              return (
                <div key={index} className={styles.tableRow}>
                  <div className={`${styles.tableCell} ${styles.productCell}`}>
                    <div className={styles.productInfo}>
                      <div className={styles.productName}>{productName}</div>
                      {productSku && <div className={styles.productSku}>SKU: {productSku}</div>}
                    </div>
                  </div>
                  <div className={styles.tableCell}>
                    <span className={styles.sizeBadge}>{item.size || '-'}</span>
                  </div>
                  <div className={styles.tableCell}>{item.quantity || 0}</div>
                  <div className={styles.tableCell}>${(item.price || 0).toFixed(2)}</div>
                  <div className={`${styles.tableCell} ${styles.subtotalCell}`}>
                    ${subtotal.toFixed(2)}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        
        <div className={styles.invoiceSummary}>
          <div className={styles.summaryRow}>
            <span>Total Items:</span>
            <span>{totalItems}</span>
          </div>
          <div className={`${styles.summaryRow} ${styles.grandTotal}`}>
            <span>Total Factura:</span>
            <span>${invoice.totalAmount.toFixed(2)}</span>
          </div>
        </div>
        
        <div className={styles.modalActions}>
          <button onClick={generatePDF} className={styles.pdfButton}>
            <span>Descargar PDF</span>
            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"></path>
              <polyline points="7 10 12 15 17 10"></polyline>
              <line x1="12" y1="15" x2="12" y2="3"></line>
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};

export default InvoiceDetailModal;
