import GeneralHeader from "./components/Header/GeneralHeader.jsx";
import { Routes, Route, Outlet } from "react-router";
import Home from "./page/Home/Home.jsx";
import Products from "./page/Products/Products.jsx";
import Contact from "./page/Contact/Contact.jsx";
import About from "./page/About/About.jsx";
import ProductDetail from "./page/Products/Details/ProductDetail.jsx";
import Admin from "./page/Admin/Admin.jsx";
import FixedWhatsapp from "./components/FixedWhatsapp/WhatsApp.jsx";
import Cart from "./components/Cart/Cart.jsx";
import { Toaster } from "react-hot-toast";

export const App = () => {
  return (
    <>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <GeneralHeader />
              <FixedWhatsapp />
              <Cart />
              <Outlet />
              <Toaster
                position="top-center"
                reverseOrder={false}
              />
            </>
          }
        >
          <Route index element={<Home />} />
          <Route path="products" element={<Products />} />
          <Route path="products/:sku" element={<ProductDetail />} />
          <Route path="contact" element={<Contact />} />
          <Route path="about" element={<About />} />
        </Route>
        <Route path="/admin" element={<Admin />} />
      </Routes>
    </>
  );
};
