import React, { useEffect, useState } from "react";
import style from "./styledHero.module.css";
import { useNavigate } from "react-router";
import useProducts from "../../../../context/useProducts.jsx";

const HeroHome = () => {
  const navigate = useNavigate();
  const { setCategory, setSearch } = useProducts();
  const [isVisible, setIsVisible] = useState(false);
  
  useEffect(() => {
    setIsVisible(true);
  }, []);

  const handleClick = (e, category) => {
    e.preventDefault();
    setCategory(category);
    setSearch("");
    navigate("/products");
  };

  const categories = [
    { name: "Nike", icon: "" },
    { name: "Adidas", icon: "" },
    { name: "Vans", icon: "" },
    { name: "LV", icon: "" },
    { name: "DC", icon: "" },
    { name: "Puma", icon: "" },
  ];

  return (
    <main id={style.heroHome} className={`${isVisible ? style.visible : ''}`}>
      <div className={style.heroContent}>
        <div className={style.textContent}>
          <h1>
            <span className={style.gradientText}>Urban Style</span>
            <span className={style.subtitle}>Elegancia urbana en cada detalle</span>
          </h1>
          <p>Descubr nuestra coleccin exclusiva de calzados premium de las mejores marcas.</p>
          
          <div className={style.ctaContainer}>
            <button 
              onClick={(e) => handleClick(e, "todos")}
              className={style.primaryButton}
            >
              Ver Coleccin
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M5 12H19M19 12L12 5M19 12L12 19" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
            <a
              href="#offersSection"
              className={style.secondaryButton}
            >
              Ofertas Especiales
            </a>
          </div>

          <div className={style.popularCategories}>
            <span>Categoras populares:</span>
            <div className={style.categoryTags}>
              {categories.map((cat) => (
                <button 
                  key={cat.name}
                  onClick={(e) => handleClick(e, cat.name)}
                  className={style.categoryTag}
                >
                  {cat.icon} {cat.name}
                </button>
              ))}
            </div>
          </div>
        </div>

        <div className={style.imageContainer}>
          <div className={style.floatingBadge}>
            <span>Calidad Premium</span>
            <span>2025</span>
          </div>
          <img 
            src="/assets/JordanHome.webp" 
            alt="Zapatillas Jordan" 
            className={style.heroImage}
            loading="eager"
          />
          <div className={style.circleDecoration}></div>
        </div>
      </div>
      
      <div className={style.scrollIndicator}>
        <span>Desliza para explorar</span>
        <div className={style.mouse}>
          <div className={style.wheel}></div>
        </div>
      </div>
    </main>
  );
};

export default HeroHome;
