import React, { useEffect, useState, useRef } from "react";
import { useParams, useNavigate } from "react-router";
import useProducts from "../../../context/useProducts";
import useCart from "../../../context/useCart.jsx";
import style from "./StyledDetail.module.css";
import { toast } from "react-hot-toast";
import Loader from "../../../components/Loader/Loader";

const ProductDetail = () => {
  const formatoMoneda = new Intl.NumberFormat("es-AR", { style: "currency", maximumFractionDigits: 0 , currency: "ARS" });
  const { sku } = useParams();
  const navigate = useNavigate();
  const { getProductBySku, loading, error } = useProducts();
  const [product, setProduct] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalAction, setModalAction] = useState(null);
  const [selectedSizes, setSelectedSizes] = useState([]);
  const [selectedQtys, setSelectedQtys] = useState({});
  const [isImageModalOpen, setIsImageModalOpen] = useState(false);
  const [imageScale, setImageScale] = useState(1);
  const [imagePosition, setImagePosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const imageRef = useRef(null);
  const modalRef = useRef(null);
  
  const WHATSAPP_PHONE = "+5493774491008";

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const productData = await getProductBySku(sku);
        setProduct(productData);
      } catch (err) {
        console.error("Error al obtener producto:", err);
      }
    };

    if (sku) {
      fetchProduct();
    }
  }, [sku, getProductBySku]);
  const canGoBack = window.history.length > 1;

    const handleOpenModal = (action) => {
    setModalAction(action);
    setSelectedSizes([]);
    setSelectedQtys({});
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleOpenImageModal = () => {
    setIsImageModalOpen(true);
    setImageScale(1);
    setImagePosition({ x: 0, y: 0 });
    // Prevenir scroll del body
    document.body.style.overflow = 'hidden';
  };

  const handleCloseImageModal = () => {
    setIsImageModalOpen(false);
    setImageScale(1);
    setImagePosition({ x: 0, y: 0 });
    // Restaurar scroll del body
    document.body.style.overflow = 'unset';
  };

  const handleImageWheel = (e) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? 0.9 : 1.1;
    setImageScale(prev => Math.min(Math.max(prev * delta, 0.5), 3));
  };

  const handleImageClick = (e) => {
    if (imageScale === 1) {
      setImageScale(2);
    } else {
      setImageScale(1);
      setImagePosition({ x: 0, y: 0 });
    }
  };

  const handleMouseDown = (e) => {
    if (imageScale > 1) {
      e.preventDefault();
      setIsDragging(true);
      setDragStart({
        x: e.clientX - imagePosition.x,
        y: e.clientY - imagePosition.y
      });
    }
  };

  const handleMouseMove = (e) => {
    if (isDragging && imageScale > 1) {
      e.preventDefault();
      setImagePosition({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y
      });
    }
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  const handleModalClick = (e) => {
    if (e.target === e.currentTarget) {
      handleCloseImageModal();
    }
  };

  const handleTouchStart = (e) => {
    if (e.touches.length === 2) {
      const touch1 = e.touches[0];
      const touch2 = e.touches[1];
      const distance = Math.sqrt(
        Math.pow(touch2.clientX - touch1.clientX, 2) +
        Math.pow(touch2.clientY - touch1.clientY, 2)
      );
      e.target.dataset.initialDistance = distance;
    }
  };

  const handleTouchMove = (e) => {
    if (e.touches.length === 2) {
      e.preventDefault();
      const touch1 = e.touches[0];
      const touch2 = e.touches[1];
      const distance = Math.sqrt(
        Math.pow(touch2.clientX - touch1.clientX, 2) +
        Math.pow(touch2.clientY - touch1.clientY, 2)
      );
      
      const initialDistance = parseFloat(e.target.dataset.initialDistance);
      if (initialDistance) {
        const scale = distance / initialDistance;
        setImageScale(prev => Math.min(Math.max(prev * scale, 0.5), 3));
        e.target.dataset.initialDistance = distance;
      }
    }
  };

  const toggleSize = (size) => {
    setSelectedSizes((prev) => {
      if (prev.includes(size)) {
        const next = prev.filter((s) => s !== size);
        setSelectedQtys((q) => {
          const { [size]: _omit, ...rest } = q;
          return rest;
        });
        return next;
      } else {
        setSelectedQtys((q) => ({ ...q, [size]: 1 }));
        return [...prev, size];
      }
    });
  };

  const changeQty = (size, value, max) => {
    const n = Math.max(1, Math.min(max ?? Infinity, Number(value) || 1));
    setSelectedQtys((q) => ({ ...q, [size]: n }));
  };

  const stepQty = (size, delta, max) => {
    setSelectedQtys((q) => {
      const current = q[size] ?? 1;
      const next = Math.max(1, Math.min((max ?? Infinity), current + delta));
      return { ...q, [size]: next };
    });
  };

    const handleConfirm = () => {
    if (modalAction === 'whatsapp') {
      if (!selectedSizes.length) return;
      const parts = selectedSizes.map((s) => {
        const requested = selectedQtys[s] ?? 1;
        return `${s} x ${requested}`;
      });
      const message = `Hola! Me interesa comprar las ${product.name} en talle/s ${parts.join(", ")}.`;
      const url = `https://wa.me/${WHATSAPP_PHONE}?text=${encodeURIComponent(message)}`;
      window.open(url, "_blank");
      handleCloseModal();
    } else if (modalAction === 'cart') {
      handleAddToCart();
    }
  };

  const handleEncargo = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (!product) return;
    
    const message = `Hola, estoy interesado en encargar las ${product.name}, Estn disponibles? Muchas gracias!`;
    const url = `https://wa.me/${WHATSAPP_PHONE}?text=${encodeURIComponent(message)}`;
    window.open(url, "_blank");
    return false;
  };

    const { addToCart, openCart } = useCart();

    const handleAddToCart = () => {
    if (!selectedSizes.length) return;
    const selections = selectedSizes.map(s => ({ size: s, quantity: selectedQtys[s] ?? 1 }));
    try {
      addToCart(product, selections);
      handleCloseModal();
      openCart();
    } catch (e) {
      toast.error(e.message);
    }
  };

  if (loading && !product) return <main className={style.productPage}><Loader /></main>;
  if (error) return <div className={style.error}>Error: {error}</div>;
  if (!product && !loading) return <div className={style.error}>Producto no encontrado</div>;
  if (!product) return null;
  if (error || !product) {
    return (
      <main className={style.productPage}>
        <header className={style.header}>
          <button
            type="button"
            className={style.backBtn}
            onClick={() => navigate(-1)}
            disabled={!canGoBack}
          >
            Volver
          </button>
        </header>
        <section className={style.notFound}>
          <h2>Producto no encontrado</h2>
          <p>El producto que buscas no existe o ha sido removido.</p>
        </section>
      </main>
    );
  }

  const allSizes = (product.sizes && product.sizes.length
    ? [...product.sizes].sort((a, b) => (Number(a.size.replace(/[^0-9.]/g, ""))||Infinity) - (Number(b.size.replace(/[^0-9.]/g, ""))||Infinity) || String(a.size).localeCompare(String(b.size))).map(s => s.size)
    : (product.talle || []).slice().sort((a, b) => (Number(a.replace(/[^0-9.]/g, ""))||Infinity) - (Number(b.replace(/[^0-9.]/g, ""))||Infinity) || String(a).localeCompare(String(b))));

  return (
    <main className={style.productPage}>
      <header className={style.header}>
        <button
          type="button"
          className={style.backBtn}
          onClick={() => navigate(-1)}
          disabled={!canGoBack}
        >
          Volver
        </button>
        <h1 className={style.title}>{product.name}</h1>
      </header>
      <section className={style.productInfo}>
        <div className={style.imageBox}>
          <img 
            src={product.image} 
            alt={product.name} 
            onClick={handleOpenImageModal}
            style={{ cursor: 'pointer' }}
          />
        </div>
        <div className={style.details}>
          <p className={style.description}>{product.description}</p>
          <div className={style.priceRow}>
            {product.discount ? (
              <>
                <span className={style.oldPrice}>{formatoMoneda.format(product.price)}</span>
                <span className={style.newPrice}>
                  {formatoMoneda.format(product.price * (1 - product.discount / 100))}
                </span>
                <span className={style.discount}>-{product.discount}% OFF</span>
              </>
            ) : (
              <span className={style.newPrice}>{formatoMoneda.format(product.price)}</span>
            )}
          </div>
          {product.stock > 0 && (
            <div className={style.dataRow}>
              <span>Talles disponibles:</span>
              <span>{allSizes.length ? allSizes.join(", ") : "No hay talles disponibles"}</span>
            </div>
          )}
          <div className={style.dataRow}>
            <span>Stock:</span>
            <span>{product.stock > 0 ? "Disponible!" : "Sin stock :("}</span>
          </div>
          <div className={style.dataRow}>
            <span>SKU:</span>
            <span>{product.sku}</span>
          </div>
          {product.stock > 0 ? (
            <form style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
                                          <button type="button" className={style.buyBtn} onClick={() => handleOpenModal('whatsapp')}>
                Pedir ahora
              </button>
              <button type="button" className={style.addToCartBtn} onClick={() => handleOpenModal('cart')}>
                Agregar al carrito
              </button>
            </form>
          ) : (
            <button className={style.buyBtn} onClick={handleEncargo}>
              Pedir encargue
            </button>
          )}
        </div>
      </section>

      {isModalOpen && (
        <div className={style.modalOverlay} onClick={handleCloseModal}>
          <div className={style.modal} onClick={(e) => e.stopPropagation()}>
            <div className={style.modalHeader}>
              <h3>Eleg tu talle</h3>
              <button className={style.modalClose} onClick={handleCloseModal}></button>
            </div>
            <div className={style.sizeGrid}>
              {allSizes.map((t) => {
                const qtyAvail = (product.sizes || []).find(s => s.size === t)?.quantity ?? null;
                const isDisabled = qtyAvail === 0;
                const isActive = selectedSizes.includes(t);
                return (
                  <div
                    key={t}
                    className={`${style.sizeItem} ${isActive ? style.sizeItemActive : ""} ${isDisabled ? style.sizeDisabled : ""}`}
                    onClick={() => !isDisabled && toggleSize(t)}
                  >
                    <div>{t}{qtyAvail !== null ? ` (${qtyAvail})` : ""}</div>
                    {isActive && (
                      <div className={style.stepper} onClick={(e) => e.stopPropagation()}>
                        <button onClick={() => stepQty(t, -1, qtyAvail)}>-</button>
                        <span>{selectedQtys[t] ?? 1}</span>
                        <button onClick={() => stepQty(t, +1, qtyAvail)}>+</button>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
                                    <div className={style.modalActions}>
              <button className={style.cancelBtn} onClick={handleCloseModal}>Cancelar</button>
              <button className={style.confirmBtn} onClick={handleConfirm} disabled={!selectedSizes.length}>
                Confirmar
              </button>
            </div>
          </div>
        </div>
      )}

      <section className={style.extraInfo}>
        <h2>Informacin adicional</h2>
        <ul>
          <li>Zapatillas de calidad PREMIUM.</li>
          <li>Talles en medidas BRASILERAS</li>
          <li>Nuevos modelos periodicamente.</li>
          <li>Atencin al cliente: <a href="tel:+5493774491008">+54 9 3774-491008</a></li>
          <li>Consultas por WhatsApp: <a href="https://wa.me/+5493774491008" target="_blank" rel="noopener noreferrer">Click aqu</a></li>
        </ul>
      </section>
      <footer className={style.footer}>
        <p> 2025 Shoeroom | Atencin 24/7</p>
      </footer>

      {isImageModalOpen && (
        <div className={style.imageModalOverlay} onClick={handleModalClick}>
          <div 
            ref={modalRef}
            className={style.imageModal} 
            onClick={(e) => e.stopPropagation()}
          >
            <button className={style.imageModalClose} onClick={handleCloseImageModal}>
              
            </button>
            <img 
              ref={imageRef}
              src={product.image} 
              alt={product.name} 
              className={`${style.imageModalImg} ${isDragging ? style.dragging : ''}`}
              style={{ transform: `scale(${imageScale}) translate(${imagePosition.x}px, ${imagePosition.y}px)` }}
              onWheel={handleImageWheel}
              onTouchStart={handleTouchStart}
              onTouchMove={handleTouchMove}
              onMouseDown={handleMouseDown}
              onMouseMove={handleMouseMove}
              onMouseUp={handleMouseUp}
              onClick={handleImageClick}
            />
          </div>
        </div>
      )}
    </main>
  );
};

export default ProductDetail;