import React, { useState, useEffect, useMemo } from 'react';
import { 
  FiSearch, 
  FiChevronDown, 
  FiChevronUp, 
  FiFileText, 
  FiCalendar, 
  FiAlertCircle, 
  FiTrash2 
} from 'react-icons/fi';
import styles from './SalesHistory.module.css';
import InvoiceDetailModal from '../InvoiceDetailModal/InvoiceDetailModal';
import ConfirmationModal from '../ConfirmationModal/ConfirmationModal';

const SalesHistory = () => {
  const [invoices, setInvoices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedInvoice, setSelectedInvoice] = useState(null);
  const [sortKey, setSortKey] = useState('createdAt');
  const [sortOrder, setSortOrder] = useState('desc');
  const [searchQuery, setSearchQuery] = useState('');
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  useEffect(() => {
    const fetchInvoices = async () => {
      try {
        const backendUrl = (import.meta.env.VITE_BACKEND_URL || 'http://localhost:4000').replace(/\/+$/, '');
        const apiUrl = `${backendUrl}/api/invoices`;
        const response = await fetch(apiUrl, {
          credentials: 'include',
          headers: {
            'Accept': 'application/json'
          }
        });
        
        if (!response.ok) {
          throw new Error(`Failed to fetch invoices: ${response.status} ${response.statusText}`);
        }
        const data = await response.json();
        setInvoices(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchInvoices();
  }, []);

  const sortedInvoices = useMemo(() => {
    let filtered = [...invoices];
    if (searchQuery) {
      const query = searchQuery.trim().toLowerCase();
      filtered = filtered.filter(invoice => {
        const invoiceNumber = invoice.invoiceNumber?.toLowerCase() || '';
        
        const date = new Date(invoice.createdAt);
        const formattedDate = date.toLocaleDateString('es-AR', {
          day: '2-digit',
          month: '2-digit',
          year: 'numeric'
        }).toLowerCase();
        
        const formattedTime = date.toLocaleTimeString('es-AR', {
          hour: '2-digit',
          minute: '2-digit'
        }).toLowerCase();
        
        const total = invoice.totalAmount ? 
          parseFloat(invoice.totalAmount).toFixed(2).toString() : '';
        
        return (
          invoiceNumber.includes(query) ||
          formattedDate.includes(query) ||
          formattedTime.includes(query) ||
          total.includes(query)
        );
      });
    }
    
    return filtered.sort((a, b) => {
      let aValue = a[sortKey];
      let bValue = b[sortKey];

      if (sortKey === 'createdAt') {
        aValue = new Date(aValue);
        bValue = new Date(bValue);
      } else if (sortKey === 'totalAmount') {
        aValue = parseFloat(aValue || 0);
        bValue = parseFloat(bValue || 0);
      } else if (sortKey === 'invoiceNumber') {
        aValue = aValue?.toString().toLowerCase() || '';
        bValue = bValue?.toString().toLowerCase() || '';
      }

      if (aValue < bValue) {
        return sortOrder === 'asc' ? -1 : 1;
      }
      if (aValue > bValue) {
        return sortOrder === 'asc' ? 1 : -1;
      }
      return 0;
    });
  }, [invoices, sortKey, sortOrder]);

  const handleViewInvoice = (invoice) => {
    setSelectedInvoice(invoice);
  };
  
  const handleCloseModal = () => {
    setSelectedInvoice(null);
  };

  const handleDeleteAllInvoices = async () => {
    try {
      setIsDeleting(true);
      setError('');
      
      const isProduction = import.meta.env.PROD;
      const baseUrl = isProduction 
        ? `${import.meta.env.VITE_BACKEND_URL}/api` 
        : '/api';
      const apiUrl = `${baseUrl}/invoices`;
      
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 10000);
      
      try {
        const response = await fetch(apiUrl, {
          method: 'DELETE',
          credentials: 'include',
          signal: controller.signal,
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
          },
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error('Error response:', {
            status: response.status,
            statusText: response.statusText,
            headers: Object.fromEntries(response.headers.entries()),
            body: errorText
          });
          throw new Error(`Error del servidor: ${response.status} ${response.statusText}`);
        }
        
        let responseData;
        try {
          responseData = await response.json();
        } catch (jsonError) {
          throw new Error('Error al procesar la respuesta del servidor');
        }

        clearTimeout(timeoutId);

        if (!response.ok) {
          const errorText = await response.text();
          console.error('Error response text:', errorText);
          throw new Error(`Server responded with status ${response.status}`);
        }

        try {
          const data = await response.json();
          
          setInvoices([]);
          setSuccessMessage('Todas las facturas se eliminaron correctamente');
          setTimeout(() => setSuccessMessage(''), 5000);
        } catch (jsonError) {
          console.error('Error parsing JSON response:', jsonError);
          throw new Error('Invalid response from server');
        }
      } catch (err) {
        console.error('Delete error:', {
          name: err.name,
          message: err.message,
          stack: err.stack
        });
        
        if (err.name === 'AbortError') {
          setError('La solicitud ha tardado demasiado. Por favor, intntalo de nuevo.');
        } else if (err.message.includes('Failed to fetch')) {
          setError('No se pudo conectar al servidor. Verifica tu conexin a internet o que el servidor est en ejecucin.');
        } else {
          setError(err.message || 'Error al eliminar las facturas');
        }
        
        throw err;
      } finally {
        setIsDeleting(false);
        setShowDeleteModal(false);
      }
      setInvoices([]);
      setShowDeleteModal(false);
      
    } catch (err) {
      console.error('Delete error details:', {
        name: err.name,
        message: err.message,
        stack: err.stack,
        response: err.response
      });
      
      let errorMessage = err.message || 'Error desconocido al eliminar las facturas';
      
      if (errorMessage.includes('Failed to fetch') || errorMessage.includes('NetworkError')) {
        errorMessage = 'Error de conexin. Por favor verifica tu conexin a internet.';
      } else if (errorMessage.includes('404')) {
        errorMessage = 'No se pudo encontrar el servidor. Por favor, verifica la URL del backend.';
      } else if (errorMessage.includes('500')) {
        errorMessage = 'Error interno del servidor. Por favor, intntalo de nuevo ms tarde.';
      } else if (errorMessage.includes('403') || errorMessage.includes('401')) {
        errorMessage = 'No tienes permisos para realizar esta accin. Por favor, inicia sesin nuevamente.';
      }
      
      setError(errorMessage);
      
      setTimeout(() => setError(''), 5000);
    } finally {
      setIsDeleting(false);
    }
  };

  const formatDate = (dateString) => {
    const options = { 
      year: 'numeric', 
      month: 'short', 
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    };
    return new Date(dateString).toLocaleDateString('es-AR', options);
  };

  if (loading) return (
    <div className={styles.loading}>
      <FiFileText size={48} />
      <p>Cargando facturas...</p>
    </div>
  );
  
  if (error) return (
    <div className={styles.error}>
      <FiAlertCircle size={24} />
      <p>Error al cargar las facturas: {error}</p>
    </div>
  );

  return (
    <div className={styles.historyContainer}>
      <div className={styles.header}>
        <h2 className={styles.title}>Historial de Ventas</h2>
        <div className={styles.controls}>
          <input
            type="text"
            placeholder="Buscar factura..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className={styles.searchInput}
          />
          <button 
            onClick={() => setShowDeleteModal(true)}
            disabled={invoices.length === 0}
            className={styles.deleteButton}
            title="Eliminar todo el historial"
          >
            <FiTrash2 />
            <span>Eliminar todo</span>
          </button>
        </div>
      </div>

      {loading ? (
        <div className={styles.loading}>Cargando facturas...</div>
      ) : error ? (
        <div className={styles.error}>Error al cargar las facturas: {error}</div>
      ) : sortedInvoices.length === 0 ? (
        <div className={styles.noInvoices}>
          {searchQuery ? 'No se encontraron facturas que coincidan con la bsqueda' : 'No hay facturas disponibles'}
        </div>
      ) : (
        <div className={styles.tableWrapper}>
          <div className={styles.tableContainer}>
            <div className={styles.column}>
              <div 
                className={`${styles.headerCell} ${styles.sortableHeader} ${sortKey === 'invoiceNumber' ? styles.active : ''}`}
                onClick={() => handleSort('invoiceNumber')}
              >
                <span>Factura</span>
                <span className={styles.sortIcon}>
                  {sortKey === 'invoiceNumber' && sortOrder === 'asc' ? <FiChevronUp /> : <FiChevronDown />}
                </span>
              </div>
              <div className={styles.columnBody}>
                {sortedInvoices.map((invoice) => (
                  <div 
                    key={`${invoice._id}-factura`} 
                    className={`${styles.cell} ${styles.clickableCell}`}
                    onClick={() => handleViewInvoice(invoice)}
                  >
                    <span className={styles.invoiceNumber}>
                      {invoice.invoiceNumber || 'N/A'}
                    </span>
                  </div>
                ))}
              </div>
            </div>

            <div className={styles.column}>
              <div 
                className={`${styles.headerCell} ${styles.sortableHeader} ${sortKey === 'createdAt' ? styles.active : ''}`}
                onClick={() => handleSort('createdAt')}
              >
                <span>Fecha</span>
                <span className={styles.sortIcon}>
                  {sortKey === 'createdAt' && sortOrder === 'asc' ? <FiChevronUp /> : <FiChevronDown />}
                </span>
              </div>
              <div className={styles.columnBody}>
                {sortedInvoices.map((invoice) => (
                  <div 
                    key={`${invoice._id}-fecha`} 
                    className={`${styles.cell} ${styles.dateCell} ${styles.clickableCell}`}
                    onClick={() => handleViewInvoice(invoice)}
                  >
                    <FiCalendar className={styles.icon} />
                    {formatDate(invoice.createdAt)}
                  </div>
                ))}
              </div>
            </div>

            <div className={styles.column}>
              <div 
                className={`${styles.headerCell} ${styles.sortableHeader} ${sortKey === 'totalAmount' ? styles.active : ''}`}
                onClick={() => handleSort('totalAmount')}
              >
                <span>Total</span>
                <span className={styles.sortIcon}>
                  {sortKey === 'totalAmount' && sortOrder === 'asc' ? <FiChevronUp /> : <FiChevronDown />}
                </span>
              </div>
              <div className={styles.columnBody}>
                {sortedInvoices.map((invoice) => (
                  <div 
                    key={`${invoice._id}-total`} 
                    className={`${styles.cell} ${styles.amountCell} ${styles.clickableCell}`}
                    onClick={() => handleViewInvoice(invoice)}
                  >
                    <span className={styles.amount}>
                      ${parseFloat(invoice.totalAmount).toFixed(2)}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
      
      {selectedInvoice && (
        <InvoiceDetailModal
          invoice={selectedInvoice}
          onClose={handleCloseModal}
        />
      )}

      <ConfirmationModal
        isOpen={showDeleteModal}
        onClose={() => setShowDeleteModal(false)}
        onConfirm={handleDeleteAllInvoices}
        title="Eliminar historial de ventas"
        message="Ests seguro de que deseas eliminar todo el historial de ventas? Esta accin no se puede deshacer."
      />
    </div>
  );
};

export default SalesHistory;
