import { createContext, useContext, useState, useEffect, useCallback } from "react";

const Catalog = createContext(null);

export const ProductsProvider = ({ children }) => {
  const PRODUCTS_PER_PAGE = 9;
  const [list, setList] = useState([]);
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [pagination, setPagination] = useState({
    page: 1,
    total: 0,
    totalPages: 1,
    limit: 12
  });
  const [search, setSearch] = useState("");
  const [category, setCategory] = useState("todos");
  const urlBackend = import.meta.env.VITE_BACKEND_URL || "http://localhost:4000";

  const fetchProducts = useCallback(async (page = 1, limit = 12) => {
    try {
      setLoading(true);
      setError(null);
      const res = await fetch(`${urlBackend}/api/products?page=${page}&limit=${limit}`);
      if (!res.ok) throw new Error("Error al obtener productos");
      const response = await res.json();
      setList(response.data || []);
      setResults(response.data || []);
      setPagination(response.pagination || {
        page: 1,
        total: response.data?.length || 0,
        totalPages: 1,
        limit
      });
      return response;
    } catch (err) {
      setError(err.message);
      console.error("Error fetching products:", err);
      return { data: [], pagination: null };
    } finally {
      setLoading(false);
    }
  }, [urlBackend]);

  const createProduct = useCallback(async (productData) => {
    try {
      setLoading(true);
      setError(null);
      const res = await fetch(`${urlBackend}/api/products`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(productData),
      });
      if (!res.ok) throw new Error("Error al crear producto");
      const newProduct = await res.json();
      setList(prev => [...prev, newProduct]);
      setResults(prev => [...prev, newProduct]);
      return newProduct;
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [urlBackend]);

  const updateProduct = useCallback(async (sku, productData) => {
    try {
      setLoading(true);
      setError(null);
      const safeSku = encodeURIComponent(sku);
      const res = await fetch(`${urlBackend}/api/products/${safeSku}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(productData),
      });
      if (!res.ok) throw new Error("Error al actualizar producto");
      const updatedProduct = await res.json();
      setList(prev => prev.map(p => p.sku === sku ? updatedProduct : p));
      setResults(prev => prev.map(p => p.sku === sku ? updatedProduct : p));
      return updatedProduct;
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [urlBackend]);

  const deleteProduct = useCallback(async (sku) => {
    try {
      setLoading(true);
      setError(null);
      const safeSku = encodeURIComponent(sku);
      const res = await fetch(`${urlBackend}/api/products/${safeSku}`, {
        method: "DELETE",
      });
      if (!res.ok) throw new Error("Error al eliminar producto");
      setList(prev => prev.filter(p => p.sku !== sku));
      setResults(prev => prev.filter(p => p.sku !== sku));
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [urlBackend]);

  const getProductBySku = useCallback(async (sku) => {
    try {
      setLoading(true);
      setError(null);
      const safeSku = encodeURIComponent(sku);
      const res = await fetch(`${urlBackend}/api/products/${safeSku}`);
      if (!res.ok) throw new Error("Producto no encontrado");
      return await res.json();
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [urlBackend]);

  const handleSearch = useCallback((searchTerm) => {
    setSearch(searchTerm);
    fetchProducts(1, 12);
  }, [fetchProducts]);

  const handleCategoryChange = useCallback((newCategory) => {
    setCategory(newCategory);
    fetchProducts(1, 12);
  }, [fetchProducts]);

  useEffect(() => {
    fetchProducts(1, 12);
  }, [fetchProducts]);

  const clearError = useCallback(() => setError(null), []);

  return (
    <Catalog.Provider
      value={{
        list,
        results,
        search,
        category,
        loading,
        error,
        pagination,
        setSearch: handleSearch,
        setCategory: handleCategoryChange,
        fetchProducts,
        createProduct,
        updateProduct,
        deleteProduct,
        getProductBySku,
        clearError,
      }}
    >
      {children}
    </Catalog.Provider>
  );
};

const useProducts = () => useContext(Catalog);
export default useProducts;
