import style from "./styledFAQ.module.css";

const FAQ = () => (
  <main className={style.faqMain}>
    <header className={style.faqHeader}>
      <h1 className={style.faqTitle}>Preguntas Frecuentes</h1>
      <p className={style.faqIntro}>
        Tens dudas? Ac respondemos las consultas ms comunes sobre envos,
        cambios, pagos y mucho ms. Si tu pregunta no est aqu, contactanos!
      </p>
    </header>
    <section className={style.faqSection} aria-label="Preguntas frecuentes">
      <dl className={style.faqList}>
        <dt className={style.faqQuestion}>Cules son los mtodos de pago?</dt>
        <dd className={style.faqAnswer}>
          Aceptamos tarjetas de crdito, dbito, transferencias bancarias y
          pagos en efectivo.
        </dd>

        <dt className={style.faqQuestion}>Realizan envos a todo el pas?</dt>
        <dd className={style.faqAnswer}>
          Proximamente.
        </dd>

        <dt className={style.faqQuestion}>
          Puedo cambiar o devolver un producto?
        </dt>
        <dd className={style.faqAnswer}>
          Solo en caso de que el producto tenga un defecto de fbrica.
          No nos responsabilizamos por daos o defectos causados por el uso
          normal del producto.
        </dd>

        <dt className={style.faqQuestion}>
          Cmo puedo efectuar una compra?
        </dt>
        <dd className={style.faqAnswer}>
          Pods acercarte a nuestro local y realizar la compra en persona.
          Tambin pods armar tu carrito en nuestra pgina, y contactarte con
          nosotros va WhatsApp para finalizar la compra.
        </dd>
      </dl>
    </section>
    <footer className={style.faqFooter}>
      <small>
        No encontraste tu respuesta? Pods consultarnos por WhatsApp:{" "}
        <a
          href="https://wa.me/+5493774491008"
          target="_blank"
          rel="noopener noreferrer"
          className={style.faqMail}
        >
          +54 9 3774-491008
        </a>
      </small>
    </footer>
    <aside className={style.faqBgEffect} aria-hidden="true"></aside>
  </main>
);

export default FAQ;
