import style from "./styledHeader.module.css";
import { NavLink, useNavigate } from "react-router";
import useProducts from "../../context/useProducts.jsx";
import { useRef, useState, useEffect } from "react";
import useCart from "../../context/useCart.jsx";
import { FiMenu, FiX, FiSearch, FiShoppingCart } from 'react-icons/fi';

const GeneralHeader = () => {
  const { setSearch } = useProducts();
  const { openCart, totalItems } = useCart();
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState("");
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const searchInputRef = useRef(null);

  const handleSearch = (e) => {
    e.preventDefault();
    const query = searchQuery.trim();
    if (query) {
      setSearch(query);
      navigate('/products');
    } else {
      setSearch('');
      navigate('/products');
    }
    setSearchQuery('');
    setIsMenuOpen(false);
    setIsSearchFocused(false);
    document.body.style.overflow = 'auto';
  };

  const handleClearSearch = (e) => {
    e?.preventDefault();
    setSearchQuery('');
    searchInputRef.current?.focus();
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      const query = searchQuery.trim();
      if (query) {
        setSearch(query);
        navigate('/products');
      } else {
        setSearch('');
        navigate('/products');
      }
      setSearchQuery('');
      setIsMenuOpen(false);
      setIsSearchFocused(false);
    }
  };

  const menuRef = useRef(null);
  const buttonRef = useRef(null);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleNavClick = () => {
    setIsMenuOpen(false);
  };

  useEffect(() => {
    if (!isMenuOpen) return;

    const handleClickOutside = (event) => {
      if (
        menuRef.current &&
        !menuRef.current.contains(event.target) &&
        buttonRef.current &&
        !buttonRef.current.contains(event.target)
      ) {
        setIsMenuOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, [isMenuOpen]);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20);
    };
    
    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { to: "/products", label: "Productos" },
    { to: "/about", label: "Nosotros" },
    { to: "/contact", label: "Contacto" },
  ];

  const mobileNav = (
    <nav 
      ref={menuRef}
      className={`${style.mobileNav} ${isMenuOpen ? style.active : ''}`}
      aria-hidden={!isMenuOpen}
      onClick={(e) => e.stopPropagation()}
    >
      <form onSubmit={handleSearch} className={style.mobileSearchForm}>
        <div className={style.mobileSearchContainer}>
          <div style={{ position: 'relative', flex: 1 }}>
            <FiSearch className={style.searchIcon} size={18} />
            <input
              ref={searchInputRef}
              type="text"
              placeholder="Buscar productos..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={handleKeyDown}
              onFocus={() => setIsSearchFocused(true)}
              onBlur={() => setTimeout(() => setIsSearchFocused(false), 200)}
              className={style.mobileSearchInput}
              aria-label="Buscar productos"
              style={{
                width: '100%',
                paddingLeft: '2.5rem',
                paddingRight: searchQuery ? '2.5rem' : '1rem'
              }}
            />
          </div>
          <button 
            type="submit" 
            className={`${style.searchButton} ${!searchQuery.trim() ? '' : ""}`}
            aria-label="Buscar"
            style={{
              whiteSpace: 'nowrap',
              marginLeft: '0.5rem'
            }}
          >
            <FiSearch size={20} />
          </button>
        </div>
      </form>
      
      {navLinks.map((link) => (
        <NavLink
          key={`mobile-${link.to}`}
          to={link.to}
          className={({ isActive }) =>
            `${style.link} ${isActive ? style.active : ''}`
          }
          onClick={handleNavClick}
        >
          {link.label}
        </NavLink>
      ))}
    </nav>
  );

  return (
    <>
      <header
        id={style.mainHeader}
        className={`${isScrolled ? style.scrolled : ""} glass-effect`}
      >
          {/* Logo */}
          <NavLink to="/" className={style.logo}>
            <h1>
              <span>SHOE</span>
              <span>ROOM</span>
            </h1>
          </NavLink>

          <nav className={style.desktopNav}>
            {navLinks.map((link) => (
              <NavLink
                key={link.to}
                to={link.to}
                className={({ isActive }) =>
                  `${style.link} ${isActive ? style.active : ""}`
                }
                onClick={handleNavClick}
              >
                {link.label}
              </NavLink>
            ))}
          </nav>

            <form onSubmit={handleSearch} className={style.searchForm}>
              <input
                type="text"
                placeholder="Buscar productos..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                onKeyDown={handleKeyDown}
                className={style.searchInput}
              />
              <button type="submit" className={style.searchButtonDesktop}>
                <FiSearch size={20} />
              </button>
            </form>

            <button 
              className={style.iconButton} 
              onClick={openCart}
              aria-label="Carrito de compras"
            >
              <FiShoppingCart size={22} />
              {totalItems > 0 && (
                <span className={style.cartBadge}>{totalItems}</span>
              )}
            </button>

            <button 
              ref={buttonRef}
              className={`${style.menuButton} ${isMenuOpen ? style.menuOpen : ''}`}
              onClick={toggleMenu}
              aria-label={isMenuOpen ? 'Cerrar men' : 'Abrir men'}
              aria-expanded={isMenuOpen}
            >
              {isMenuOpen ? <FiX size={24} /> : <FiMenu size={24} />}
            </button>
        {mobileNav}
      </header>
    </>
  );
};

export default GeneralHeader;
