import { useState, useEffect, useRef } from 'react';
import { CSSTransition } from 'react-transition-group';
import useCart from '../../context/useCart.jsx';
import useProducts from '../../context/useProducts.jsx';
import styles from './Cart.module.css';

const EditItemModal = ({ sku, onClose, onSave }) => {
  const { items } = useCart();
  const { list } = useProducts();
  const product = list.find(p => p.sku === sku);
  const item = items.find(i => i.sku === sku);
  const [localSel, setLocalSel] = useState(item ? item.selections.map(s => ({ ...s })) : []);
    const formatoMoneda = new Intl.NumberFormat("es-AR", { style: "currency", currency: "ARS", maximumFractionDigits: 0 });

  const maxFor = (size) => (product?.sizes || []).find(s => s.size === size)?.quantity ?? Infinity;

  const step = (size, delta) => {
    setLocalSel(prev => prev.map(s => 
      s.size === size 
        ? { ...s, quantity: Math.max(1, Math.min(maxFor(size), s.quantity + delta)) } 
        : s
    ));
  };

  const handleSave = () => {
    onSave(sku, localSel);
    onClose();
  };

  return (
    <div className={styles.modalOverlay} onClick={onClose}>
      <div className={styles.modalContent} onClick={e => e.stopPropagation()}>
        <header className={styles.modalHeader}>
          <h3>Editar talles</h3>
          <button onClick={onClose} className={styles.closeButton} aria-label="Cerrar modal">&times;</button>
        </header>
        <ul className={styles.productsList}>
          {items.map(item => (
            <li key={item.sku} className={styles.productItem}>
              <img src={item.image} alt={item.name} className={styles.productImage} />
              <div className={styles.productInfo}>
                <h4 className={styles.productName}>{item.name}</h4>
                <div className={styles.productSizes}>
                  {item.selections.map(selection => (
                    <div key={selection.size} className={styles.sizeInfo}>
                      <span>Talle: {selection.size}</span>
                      <span>Cantidad: {selection.quantity}</span>
                    </div>
                  ))}
                </div>
                <p className={styles.productPrice}>{formatoMoneda.format((item.price * (1 - item.discount / 100)))}</p>
              </div>
              <button onClick={() => removeFromCart(item.sku)} className={styles.removeButton}>&times;</button>
            </li>
          ))}
        </ul>
        <div className={styles.sizeGrid}>
          {localSel.map(s => (
            <div key={s.size} className={styles.sizeItem}>
              <div className={styles.sizeLabel}>{s.size}</div>
              <div className={styles.quantityControl}>
                <button onClick={() => step(s.size, -1)} className={styles.quantityButton}>-</button>
                <span className={styles.quantity}>{s.quantity}</span>
                <button onClick={() => step(s.size, 1)} className={styles.quantityButton}>+</button>
              </div>
              <div className={styles.stockInfo}>Disp: {maxFor(s.size)}</div>
            </div>
          ))}
        </div>
        <footer className={styles.modalFooter}>
          <button onClick={onClose} className={styles.secondaryButton}>Cancelar</button>
          <button onClick={handleSave} className={styles.primaryButton}>Guardar</button>
        </footer>
      </div>
    </div>
  );
};

const Cart = () => {
    const {
    items,
    isOpen,
    closeCart,
    removeFromCart,
    updateItemSelections,
    updateItemQuantity,
    totalItems,
    totalCost,
    buildWhatsAppMessage,
  } = useCart();

  const { list } = useProducts();
  const [editingSku, setEditingSku] = useState(null);
  const cartRef = useRef(null);
  const nodeRef = useRef(null);

  const WHATSAPP_PHONE = '+5493774491008';

    const handleWhatsApp = (e) => {
    e.preventDefault();
    if (totalItems === 0) {
      alert('Tu carrito est vaco. Agrega productos antes de continuar.');
      return;
    }
    const url = `https://wa.me/${WHATSAPP_PHONE}?text=${encodeURIComponent(buildWhatsAppMessage())}`;
    window.open(url, '_blank');
  };

    const handleClickOutside = (e) => {
    if (e.target.closest('#cart-toggle-button')) return;
    if (cartRef.current && !cartRef.current.contains(e.target)) {
      closeCart();
    }
  };

  const handleEscape = (e) => {
    if (e.key === 'Escape') closeCart();
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
      document.addEventListener('mousedown', handleClickOutside);
      document.addEventListener('keydown', handleEscape);
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleEscape);
    };
  }, [isOpen]);

  return (
    <CSSTransition
      in={isOpen}
      timeout={300}
      classNames={{ 
        enter: styles.cartEnter,
        enterActive: styles.cartEnterActive,
        exit: styles.cartExit,
        exitActive: styles.cartExitActive,
      }}
      unmountOnExit
      nodeRef={nodeRef}
    >
      <div ref={nodeRef} className={styles.overlay} onClick={closeCart}>
      <aside ref={cartRef} className={styles.cart} onClick={e => e.stopPropagation()} role="dialog" aria-modal="true" aria-labelledby="cart-title">
        <header className={styles.header}>
          <h2 id="cart-title" className={styles.title}>Tu carrito {totalItems > 0 && `(${totalItems})`}</h2>
          <button type="button" onClick={closeCart} className={styles.closeButton} aria-label="Cerrar carrito">&times;</button>
        </header>

                <ul className={styles.productsList}>
          {items.map(item => (
            <li key={item.sku} className={styles.productItem}>
              <img src={item.image} alt={item.name} className={styles.productImage} />
              <div className={styles.productInfo}>
                <h4 className={styles.productName}>{item.name}</h4>
                                <div className={styles.productSizes}>
                  {item.selections.map(selection => (
                    <div key={selection.size} className={styles.sizeInfo}>
                      <span>Talle: {selection.size}</span>
                      <div className={styles.quantityControl}>
                        <button onClick={() => updateItemQuantity(item.sku, selection.size, -1)} className={styles.quantityButton}>-</button>
                        <span>{selection.quantity}</span>
                        <button onClick={() => updateItemQuantity(item.sku, selection.size, 1)} className={styles.quantityButton}>+</button>
                      </div>
                    </div>
                  ))}
                </div>
                                <div className={styles.priceDetails}>
                  <p className={styles.productPrice}>
                                        ${Math.round(item.price * (1 - item.discount / 100))} c/u
                  </p>
                  <p className={styles.productSubtotal}>
                                        Subtotal: ${
                      Math.round(
                        item.selections.reduce((acc, sel) => acc + sel.quantity, 0) *
                        (item.price * (1 - item.discount / 100))
                      )
                    }
                  </p>
                </div>
              </div>
              <button onClick={() => removeFromCart(item.sku)} className={styles.removeButton}>&times;</button>
            </li>
          ))}
        </ul>

        <footer className={styles.footer}>
          <div className={styles.total}>
            <span>Total</span>
                        <span>${totalCost.toFixed(0)}</span>
          </div>
                    <button className={styles.checkoutButton} onClick={handleWhatsApp} disabled={items.length === 0}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
              <path fill="#ffffff" d="M380.9 97.1C339 55.1 283.2 32 223.9 32c-122.4 0-222 99.6-222 222 0 39.1 10.2 77.3 29.6 111L0 480l117.7-30.9c32.4 17.7 68.9 27 106.1 27h.1c122.3 0 224.1-99.6 224.1-222 0-59.3-25.2-115-67.1-157zm-157 341.6c-33.8 0-65.7-10.8-94-31.5l-6.7-4-69.8 18.3L72 359.2l-4.5-7c-22.7-35.1-35.1-76.4-35.1-119.3 0-108.8 88.4-197.3 197.3-197.3 53.1 0 102.8 20.7 139.7 57.6 36.9 36.9 57.6 86.6 57.6 139.7-.2 108.9-88.6 197.3-197.5 197.3zm108.9-161.3c-6.2-3.1-36.7-18.2-42.4-20.3-5.7-2.1-9.8-3.1-13.9 3.1-4.1 6.2-16.1 20.3-19.8 24.3-3.7 4.1-7.4 4.6-13.7 1.5-6.2-3.1-26.2-9.6-49.9-30.7-18.5-16.4-31-36.7-34.7-42.4-3.7-5.7-.4-8.8 2.8-11.8 2.9-2.6 6.2-6.7 9.3-10.1 3.1-3.1 4.1-5.7 6.2-9.8 2.1-4.1 1-7.7-1.5-10.8-2.6-3.1-13.9-33.5-19-45.9-5.1-12.4-10.3-10.8-14.4-10.8h-12.4c-4.1 0-10.8 1.5-16.4 7.7-5.7 6.2-21.8 21.2-21.8 51.8 0 30.6 22.3 60.1 25.4 64.1 3.1 4.1 43.5 66.4 105.8 93.4 14.8 6.5 26.4 10.3 35.7 13.2 14.3 4.5 27.4 3.9 37.8 2.3 11.4-1.8 36.7-15 41.8-29.4 5.1-14.4 5.1-26.9 3.6-29.4-1.5-2.5-5.7-4.1-11.8-7.2z"/>
            </svg>
            Pedir por WhatsApp
          </button>
        </footer>

        {editingSku && (
          <EditItemModal sku={editingSku} onClose={() => setEditingSku(null)} onSave={updateItemSelections} />
        )}
      </aside>
    </div>
    </CSSTransition>
  );
};

export default Cart;
