import React, { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import useProducts from '../../context/useProducts';
import styles from './InvoiceForm.module.css';
import { toast } from 'react-hot-toast';

const getUniqueSizes = (products) => {
  const sizes = new Set();
  products.forEach(product => {
    product.sizes?.forEach(size => {
      if (size.quantity > 0) {
        sizes.add(size.size);
      }
    });
  });
  return Array.from(sizes).sort((a, b) => a - b);
};

const InvoiceForm = () => {
  const { list: products, loading, error: productsError, fetchProducts } = useProducts();
  const [invoiceItems, setInvoiceItems] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSizes, setSelectedSizes] = useState(new Set());
  const [formError, setFormError] = useState('');
  const [showRefresh, setShowRefresh] = useState(false);
  const [activeProduct, setActiveProduct] = useState(null);

  const availableSizes = useMemo(() => getUniqueSizes(products), [products]);

  const handleSearch = (e) => setSearchTerm(e.target.value);

  const toggleSizeSelection = (size) => {
    const newSizes = new Set(selectedSizes);
    newSizes.has(size) ? newSizes.delete(size) : newSizes.add(size);
    setSelectedSizes(newSizes);
  };

  const updateInvoiceItem = (productId, size, field, value) => {
    setInvoiceItems(invoiceItems.map(item => {
      if (item.product._id === productId && item.size === size) {
        const product = products.find(p => p._id === productId);
        const maxQuantity = product.sizes.find(s => s.size === (field === 'size' ? value : size))?.quantity || 0;

        if (field === 'quantity') {
          const newQuantity = Math.max(1, Math.min(Number(value), maxQuantity));
          return { ...item, quantity: newQuantity };
        }
        if (field === 'size') {
          const newSizeMax = product.sizes.find(s => s.size === value)?.quantity || 0;
          const newQuantity = Math.min(item.quantity, newSizeMax);
          return { ...item, size: value, quantity: newQuantity };
        }
      }
      return item;
    }));
  };

  const handleSizeSelect = (product, size) => {
    const sizeInfo = product.sizes.find(s => s.size === size);
    if (!sizeInfo || sizeInfo.quantity <= 0) {
      setFormError('No hay stock disponible para el talle seleccionado');
      return;
    }

    setInvoiceItems(prevItems => {
      const existingItemIndex = prevItems.findIndex(
        item => item.product._id === product._id && item.size === size
      );

      if (existingItemIndex >= 0) {
        const updatedItems = [...prevItems];
        updatedItems[existingItemIndex] = {
          ...updatedItems[existingItemIndex],
          quantity: updatedItems[existingItemIndex].quantity + 1
        };
        return updatedItems;
      } else {
        const newItem = {
          product: {
            _id: product._id,
            name: product.name,
            sku: product.sku,
            price: product.price,
            discount: product.discount || 0,
            sizes: product.sizes || []
          },
          quantity: 1,
          size,
          price: product.price * (1 - (product.discount || 0) / 100)
        };
        return [...prevItems, newItem];
      }
    });
    
    setActiveProduct(null);
  };

  const removeInvoiceItem = (productId, size) => {
    setInvoiceItems(prevItems => {
      const itemIndex = prevItems.findIndex(item => 
        item.product._id === productId && item.size === size
      );
      
      if (itemIndex === -1) return prevItems;
      
      if (prevItems[itemIndex].quantity > 1) {
        const updatedItems = [...prevItems];
        updatedItems[itemIndex] = {
          ...updatedItems[itemIndex],
          quantity: updatedItems[itemIndex].quantity - 1
        };
        return updatedItems;
      }
      
      return prevItems.filter((_, index) => index !== itemIndex);
    });
  };

  const totalAmount = useMemo(() => invoiceItems.reduce((acc, item) => acc + item.price * item.quantity, 0), [invoiceItems]);

  const filteredProducts = useMemo(() => {
    return products.filter(product => {
      const matchesSearch = !searchTerm ||
        product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.sku.toLowerCase().includes(searchTerm.toLowerCase());
      const hasSelectedSizes = selectedSizes.size === 0 ||
        product.sizes?.some(size => selectedSizes.has(size.size) && size.quantity > 0);
      return matchesSearch && hasSelectedSizes;
    });
  }, [products, searchTerm, selectedSizes]);

  const handleRefreshProducts = () => {
    fetchProducts();
    setInvoiceItems([]);
    setFormError('');
    setShowRefresh(false);
  };

  const handleCreateInvoice = async () => {
    if (invoiceItems.length === 0) {
      setFormError('Debe agregar al menos un producto a la factura.');
      return;
    }

    try {
      const itemsForRequest = invoiceItems.map(item => ({
        product: item.product._id,
        productSku: item.product.sku,
        size: item.size,
        quantity: item.quantity,
        price: item.price,
        productName: item.product.name
      }));

      const response = await fetch(`${import.meta.env.VITE_BACKEND_URL}/api/invoices`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Accept': 'application/json' },
        body: JSON.stringify({ items: itemsForRequest, totalAmount }),
        credentials: 'include',
        mode: 'cors'
      });

      const responseData = await response.json();

      if (!response.ok) {
        throw new Error(responseData.message || `Error al crear la factura (Cdigo: ${response.status})`);
      }

      toast.success('Factura creada con xito!');
      setInvoiceItems([]);
      setFormError('');
    } catch (err) {
      console.error('Error creating invoice:', err);
      setFormError(err.message);
      setShowRefresh(true);
    }
  };

  const containerVariants = { hidden: { opacity: 0 }, visible: { opacity: 1, transition: { staggerChildren: 0.1 } } };
  const itemVariants = { hidden: { y: 20, opacity: 0 }, visible: { y: 0, opacity: 1, transition: { duration: 0.3 } }, exit: { y: -20, opacity: 0, transition: { duration: 0.2 } } };
  const sizeButtonVariants = { initial: { scale: 1 }, tap: { scale: 0.95 }, selected: { scale: 1.05, backgroundColor: 'var(--cta-button)', color: 'white', boxShadow: '0 2px 8px rgba(0,0,0,0.2)' }, hover: { scale: 1.03, backgroundColor: 'var(--cta-button-hover)', color: 'white' } };

  return (
    <motion.div className={styles.invoiceFormContainer} initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 0.3 }}>
      <AnimatePresence>
      </AnimatePresence>

      <motion.div className={styles.formGrid} initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.1 }}>
        <motion.div className={styles.productSelection}>
          <motion.div className={styles.searchContainer}>
            <motion.input type="text" placeholder="Buscar producto por nombre o SKU..." value={searchTerm} onChange={handleSearch} className={styles.searchInput} />
          </motion.div>

          <motion.div className={styles.sizeFilter}>
            <span>Filtrar por talle:</span>
            <motion.div className={styles.sizeButtons} variants={containerVariants} initial="hidden" animate="visible">
              <AnimatePresence>
                {availableSizes.map(size => (
                  <motion.button key={size} type="button" className={`${styles.sizeButton} ${selectedSizes.has(size) ? styles.selected : ''}`} onClick={() => toggleSizeSelection(size)} variants={itemVariants} whileHover={selectedSizes.has(size) ? "selected" : "hover"} whileTap="tap" initial="initial" animate={selectedSizes.has(size) ? "selected" : "initial"}>
                    {size}
                  </motion.button>
                ))}
                {selectedSizes.size > 0 && (
                  <motion.button type="button" className={styles.clearFilter} onClick={() => setSelectedSizes(new Set())} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }} layout>
                    Limpiar
                  </motion.button>
                )}
              </AnimatePresence>
            </motion.div>
          </motion.div>

          <motion.div className={styles.productsGrid} variants={containerVariants} initial="hidden" animate="visible">
            <AnimatePresence>
              {filteredProducts.map(product => {
                const availableSizes = product.sizes?.filter(s => s.quantity > 0) || [];
                const hasMultipleSizes = availableSizes.length > 1;
                
                return (
                  <motion.div 
                    key={product._id} 
                    className={`${styles.productCard} ${activeProduct === product._id ? styles.active : ''}`} 
                    onClick={() => setActiveProduct(activeProduct === product._id ? null : product._id)} 
                    variants={itemVariants} 
                    layout 
                    transition={{ type: 'spring', stiffness: 300, damping: 30 }}
                  >
                    <div className={styles.productInfo}>
                      <h3>{product.name}</h3>
                      <p className={styles.productSku}>SKU: {product.sku}</p>
                      <p className={styles.productPrice}>
                        {product.discount > 0 ? (
                          <>
                            <span className={styles.originalPrice}>${product.price.toFixed(2)}</span>
                            <span className={styles.discountedPrice}>
                              <span className={styles.priceMinus}>
                              ${(product.price * (1 - product.discount / 100)).toFixed(2)}
                              </span>
                              <span className={styles.discountBadge}>{product.discount}% OFF</span>
                            </span>
                          </>
                        ) : (
                          <span>${product.price.toFixed(2)}</span>
                        )}
                      </p>
                      
                      <div className={styles.availableSizes}>
                        {availableSizes.length > 0 ? (
                          availableSizes.map(size => (
                            <span 
                              key={size.size} 
                              className={styles.sizeBadge}
                              onClick={(e) => {
                                e.stopPropagation();
                                handleSizeSelect(product, size.size);
                              }}
                            >
                              {size.size} ({size.quantity})
                            </span>
                          ))
                        ) : (
                          <span className={styles.outOfStock}>Sin Stock</span>
                        )}
                      </div>
                    </div>

                    {hasMultipleSizes && (
                      <AnimatePresence>
                        {activeProduct === product._id && (
                          <motion.div 
                            className={styles.sizeSelector} 
                            initial={{ height: 0, opacity: 0 }} 
                            animate={{ height: 'auto', opacity: 1 }} 
                            exit={{ height: 0, opacity: 0 }}
                            onClick={(e) => e.stopPropagation()}
                          >
                            <div className={styles.sizeGrid}>
                              {availableSizes.map(size => (
                                <motion.button 
                                  key={size.size} 
                                  className={`${styles.sizeButton} ${invoiceItems.some(i => i.product._id === product._id && i.size === size.size) ? styles.selected : ''}`} 
                                  onClick={() => handleSizeSelect(product, size.size)} 
                                  variants={sizeButtonVariants} 
                                  whileHover="hover" 
                                  whileTap="tap"
                                >
                                  {size.size}
                                  <span className={styles.sizeQuantity}>({size.quantity})</span>
                                </motion.button>
                              ))}
                            </div>
                          </motion.div>
                        )}
                      </AnimatePresence>
                    )}
                  </motion.div>
                );
              })}
            </AnimatePresence>
          </motion.div>
        </motion.div>

        <motion.div className={styles.invoiceSection}>
          <h2>Resumen de Factura</h2>
          {invoiceItems.length > 0 ? (
            <>
              <div className={styles.invoiceItems}>
                {Object.entries(
                  invoiceItems.reduce((acc, item) => {
                    if (!acc[item.product._id]) {
                      acc[item.product._id] = {
                        product: item.product,
                        sizes: {}
                      };
                    }
                    acc[item.product._id].sizes[item.size] = item;
                    return acc;
                  }, {})
                ).map(([productId, { product, sizes }]) => {
                  const sizeItems = Object.values(sizes);
                  const totalQuantity = sizeItems.reduce((sum, item) => sum + item.quantity, 0);
                  const subtotal = sizeItems.reduce(
                    (sum, item) => sum + (item.price * item.quantity),
                    0
                  );
                  const unitPrice = product.price * (1 - (product.discount || 0) / 100);

                  return (
                    <div key={product._id} className={styles.invoiceItem}>
                      <div className={styles.itemHeader}>
                        <h3>{product.name}</h3>
                        {product.discount > 0 && (
                          <span className={styles.discountBadge}>{product.discount}% OFF</span>
                        )}
                        <span className={styles.productSku}>SKU: {product.sku}</span>
                      </div>
                      <div className={styles.priceSummary}>
                        <div className={styles.priceRow}>
                          <span>Precio unitario:</span>
                          <div className={styles.priceContainer}>
                            {product.discount > 0 ? (
                              <>
                                <span className={styles.originalPrice}>${product.price.toFixed(2)}</span>
                                <span className={styles.discountBadge}> (${unitPrice.toFixed(2)})</span>
                              </>
                            ) : (
                              <span>${unitPrice.toFixed(2)}</span>
                            )}
                          </div>
                        </div>
                        <div className={styles.priceRow}>
                          <span>Subtotal:</span>
                              <span className={styles.subtotal}>${subtotal.toFixed(2)}</span>
                        </div>
                      </div>
                      <div className={styles.sizeQuantities}>
                        {sizeItems.map(item => (
                          <div key={`${product._id}-${item.size}`} className={styles.sizeRow}>
                            <div className={styles.sizeInfo}>
                              <span className={styles.sizeLabel}>Talle {item.size}</span>
                            </div>
                            <div className={styles.quantityControl}>
                              <button 
                                onClick={(e) => {
                                  e.stopPropagation();
                                  updateInvoiceItem(product._id, item.size, 'quantity', item.quantity - 1);
                                }}
                              >-</button>
                              <input 
                                type="number" 
                                min="1" 
                                value={item.quantity}
                                onChange={(e) => {
                                  const newQuantity = parseInt(e.target.value) || 1;
                                  updateInvoiceItem(product._id, item.size, 'quantity', newQuantity);
                                }}
                              />
                              <button 
                                onClick={(e) => {
                                  e.stopPropagation();
                                  updateInvoiceItem(product._id, item.size, 'quantity', item.quantity + 1);
                                }}
                              >+</button>
                            </div>
                            <button 
                              className={styles.removeSizeButton}
                              onClick={(e) => {
                                e.stopPropagation();
                                setInvoiceItems(prev => 
                                  prev.filter(i => !(i.product._id === product._id && i.size === item.size))
                                );
                              }}
                              aria-label={`Eliminar talle ${item.size}`}
                            >
                              
                            </button>
                          </div>
                        ))}
                      </div>
                      
                      
                    </div>
                  );
                })}
              </div>

              <div className={styles.invoiceSummary}>
                <div className={`${styles.summaryRow} ${styles.total}`}><span>Total:</span><span>${(totalAmount).toFixed(2)}</span></div>
              </div>

              <button onClick={handleCreateInvoice} className={styles.createInvoiceButton} disabled={loading || invoiceItems.length === 0}>
                {loading ? 'Procesando...' : 'Generar Factura'}
              </button>
            </>
          ) : (
            <motion.div className={styles.emptyState}>
              <p>No hay productos en la factura</p>
            </motion.div>
          )}
        </motion.div>
      </motion.div>
    </motion.div>
  );
};

export default InvoiceForm;
