import { useState, useMemo } from "react";
import useProducts from "../../context/useProducts";
import ProductForm from "../ProductForm/ProductForm";
import InvoiceForm from '../InvoiceForm/InvoiceForm';
import SalesHistory from '../SalesHistory/SalesHistory';
import styles from "./AdminPanel.module.css";

const AdminPanel = ({ onLogout }) => {
  const { 
    list, 
    loading, 
    error, 
    deleteProduct, 
    clearError 
  } = useProducts();
  
  const [showForm, setShowForm] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  const [confirmDelete, setConfirmDelete] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [categoryFilter, setCategoryFilter] = useState("todos");
  const [stockFilter, setStockFilter] = useState("todos");
  const [discountFilter, setDiscountFilter] = useState("todos");
  const [sortBy, setSortBy] = useState("name");
  const [sortOrder, setSortOrder] = useState("asc");
  const [activeTab, setActiveTab] = useState('products');
  const [sizeFilter, setSizeFilter] = useState("todos");

  const availableSizes = useMemo(() => {
    const sizeMap = new Map();
    
    const addSize = (size, quantity = 1) => {
      if (!size) return;
      const normalizedSize = size.toString().toLowerCase().trim();
      if (normalizedSize) {
        sizeMap.set(normalizedSize, (sizeMap.get(normalizedSize) || 0) + quantity);
      }
    };
    
    list.forEach(product => {
      if (Array.isArray(product.talle)) {
        product.talle.forEach(t => {
          if (t) addSize(t);
        });
      }
      
      if (Array.isArray(product.sizes)) {
        product.sizes.forEach(s => {
          if (!s) return;
          
          if (typeof s === 'object' && s !== null && 'size' in s) {
            if (s.size) {
              const quantity = Number(s.quantity) || 1;
              addSize(s.size, quantity);
            }
          } else {
            addSize(s);
          }
        });
      }
      
      if (Array.isArray(product.stock)) {
        product.stock.forEach(s => {
          if (!s) return;
          
          if (typeof s === 'object' && s !== null && 'size' in s) {
            if (s.size) {
              const quantity = Number(s.quantity) || 1;
              addSize(s.size, quantity);
            }
          } else {
            addSize(s);
          }
        });
      }
      
      if (product.size) {
        addSize(product.size);
      }
    });
    
    return Array.from(sizeMap.entries())
      .filter(([_, count]) => count > 0)
      .map(([size, count]) => ({
        size,
        count
      }))
      .sort((a, b) => {
        const numA = parseInt(a.size, 10);
        const numB = parseInt(b.size, 10);
        
        if (!isNaN(numA) && !isNaN(numB)) {
          return numA - numB;
        }
        
        return a.size.localeCompare(b.size);
      });
  }, [list]);
  
  const totalUnitsInStock = useMemo(() => {
    return list.reduce((acc, p) => acc + (Number(p.stock) || 0), 0);
  }, [list]);
  const filteredAndSortedProducts = useMemo(() => {
    let filtered = list;

    if (searchTerm.trim()) {
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.sku.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.description?.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (categoryFilter !== "todos") {
      filtered = filtered.filter(product => product.category === categoryFilter);
    }

    if (stockFilter === "conStock") {
      filtered = filtered.filter(product => product.stock > 0);
    } else if (stockFilter === "sinStock") {
      filtered = filtered.filter(product => product.stock === 0);
    }

    if (discountFilter === "conDescuento") {
      filtered = filtered.filter(product => product.discount > 0);
    } else if (discountFilter === "sinDescuento") {
      filtered = filtered.filter(product => product.discount === 0);
    }

    if (sizeFilter !== "todos") {
      const targetSize = sizeFilter.toString().toLowerCase().trim();
      filtered = filtered.filter(product => {
        if (Array.isArray(product.talle)) {
          const hasInTalle = product.talle.some(t => 
            t && t.toString().toLowerCase().trim() === targetSize
          );
          if (hasInTalle) return true;
        }
        
        if (Array.isArray(product.sizes)) {
          const hasInSizes = product.sizes.some(s => {
            if (!s) return false;
            if (typeof s === 'object' && s !== null && 'size' in s) {
              return s.size && s.size.toString().toLowerCase().trim() === targetSize;
            }
            return s.toString().toLowerCase().trim() === targetSize;
          });
          if (hasInSizes) return true;
        }
        
        if (Array.isArray(product.stock)) {
          const hasInStock = product.stock.some(s => {
            if (!s) return false;
            if (typeof s === 'object' && s !== null && 'size' in s) {
              return s.size && s.size.toString().toLowerCase().trim() === targetSize;
            }
            return s.toString().toLowerCase().trim() === targetSize;
          });
          if (hasInStock) return true;
        }
        
        if (product.size) {
          return product.size.toString().toLowerCase().trim() === targetSize;
        }
        
        return false;
      });
    }

    filtered.sort((a, b) => {
      let aValue, bValue;

      switch (sortBy) {
        case "name":
          aValue = a.name.toLowerCase();
          bValue = b.name.toLowerCase();
          break;
        case "price":
          aValue = a.price;
          bValue = b.price;
          break;
        case "stock":
          aValue = a.stock;
          bValue = b.stock;
          break;
        case "discount":
          aValue = a.discount || 0;
          bValue = b.discount || 0;
          break;
        case "category":
          aValue = a.category.toLowerCase();
          bValue = b.category.toLowerCase();
          break;
        default:
          aValue = a.name.toLowerCase();
          bValue = b.name.toLowerCase();
      }

      if (sortOrder === "asc") {
        return aValue > bValue ? 1 : -1;
      } else {
        return aValue < bValue ? 1 : -1;
      }
    });

    return filtered;
  }, [list, searchTerm, categoryFilter, stockFilter, discountFilter, sortBy, sortOrder]);

  const handleCreateProduct = () => {
    setEditingProduct(null);
    setShowForm(true);
  };

  const handleEditProduct = (product) => {
    setEditingProduct(product);
    setShowForm(true);
  };

  const handleDeleteProduct = async (sku) => {
    try {
      await deleteProduct(sku);
      setConfirmDelete(null);
    } catch (err) {
      console.error("Error al eliminar producto:", err);
    }
  };

  const handleCloseForm = () => {
    setShowForm(false);
    setEditingProduct(null);
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('es-AR', {
      style: 'currency',
      currency: 'ARS'
    }).format(price);
  };

  const clearFilters = () => {
    setSearchTerm("");
    setCategoryFilter("todos");
    setStockFilter("todos");
    setDiscountFilter("todos");
    setSizeFilter("todos");
    setSortBy("name");
    setSortOrder("asc");
  };

  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const renderSizes = (sizes = [], talle = []) => {
    const num = (v) => {
      if (v === null || v === undefined) return Infinity;
      const n = Number(String(v).replace(/[^0-9.]/g, ""));
      return isNaN(n) ? Infinity : n;
    };
    
    try {
      if (Array.isArray(sizes) && sizes.length > 0) {
        if (sizes[0] && typeof sizes[0] === 'object' && sizes[0] !== null) {
          const ordered = sizes
            .filter(s => s && (s.size !== undefined && s.size !== null))
            .sort((a, b) => num(a.size) - num(b.size) || String(a.size).localeCompare(String(b.size)));
          
          if (ordered.length > 0) {
            return ordered.map(s => {
              const size = s.size !== undefined ? s.size : '';
              const quantity = s.quantity !== undefined ? s.quantity : 0;
              return `${size}:${quantity}`;
            }).join("  ");
          }
        }
        else if (sizes.length > 0 && (typeof sizes[0] === 'string' || typeof sizes[0] === 'number')) {
          const ordered = [...sizes]
            .filter(t => t !== null && t !== undefined)
            .sort((a, b) => num(a) - num(b) || String(a).localeCompare(String(b)));
          return ordered.join(", ");
        }
      }
      
      if (Array.isArray(talle) && talle.length > 0) {
        const ordered = [...talle]
          .filter(t => t !== null && t !== undefined)
          .sort((a, b) => num(a) - num(b) || String(a).localeCompare(String(b)));
        return ordered.join(", ");
      }
      
      return "Sin talles";
    } catch (error) {
      console.error("Error al renderizar talles:", error, { sizes, talle });
      return "Error al cargar talles";
    }
  };

  if (loading && list.length === 0) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.spinner}></div>
        <p>Cargando productos...</p>
      </div>
    );
  }

  return (
    <div className={styles.adminPanel}>
      <div className={styles.header}>
        <h1>Panel de Administracin</h1>
        <div className={styles.headerActions}>
          <button onClick={handleCreateProduct} className={styles.createButton}>
            + Crear
          </button>
          <button onClick={handleScrollTop} className={styles.scrollTopButton} title="Subir">
            
          </button>
          {onLogout && (
            <button onClick={onLogout} className={styles.logoutButtonPanel}>
              Salir
            </button>
          )}
        </div>
      </div>

      <div className={styles.tabNavigation}>
        <button 
          className={`${styles.tabButton} ${activeTab === 'products' ? styles.activeTab : ''}`}
          onClick={() => setActiveTab('products')}
        >
          Gestin de Productos
        </button>
        <button 
          className={`${styles.tabButton} ${activeTab === 'invoicing' ? styles.activeTab : ''}`}
          onClick={() => setActiveTab('invoicing')}
        >
          Facturacin
        </button>
        <button 
          className={`${styles.tabButton} ${activeTab === 'sales' ? styles.activeTab : ''}`}
          onClick={() => setActiveTab('sales')}
        >
          Historial de Ventas
        </button>
      </div>

      {error && (
        <div className={styles.error}>
          {error}
          <button onClick={clearError}></button>
        </div>
      )}

      {activeTab === 'products' && (
        <>
          <div className={styles.stats}>
            <div className={styles.statCard}>
              <h3>Total Productos</h3>
              <p>{list.length}</p>
            </div>
            <div className={styles.statCard}>
              <h3>Unidades en stock</h3>
              <p>{totalUnitsInStock}</p>
            </div>
            <div className={styles.statCard}>
              <h3>Sin Stock</h3>
              <p>{list.filter(p => Number(p.stock) === 0).length}</p>
            </div>
            <div className={styles.statCard}>
              <h3>Con Descuento</h3>
              <p>{list.filter(p => Number(p.discount) > 0).length}</p>
            </div>
          </div>
          <div className={styles.filtersSection}>
            <div className={styles.searchBox}>
              <input
                type="text"
                placeholder="Buscar por nombre, SKU o descripcin..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className={styles.searchInput}
              />
            </div>

            <div className={styles.filtersRow}>
              <div className={styles.filterGroup}>
                <label>Categora:</label>
                <select 
                  value={categoryFilter} 
                  onChange={(e) => setCategoryFilter(e.target.value)}
                  className={styles.select}
                >
                  <option value="todos">Todas</option>
                  <option value="Nike">Nike</option>
                  <option value="Adidas">Adidas</option>
                  <option value="Vans">Vans</option>
                  <option value="LV">LV</option>
                  <option value="DC">DC</option>
                  <option value="Puma">Puma</option>
                </select>
              </div>

              <div className={styles.filterGroup}>
                <label>Stock:</label>
                <select
                  value={stockFilter}
                  onChange={(e) => setStockFilter(e.target.value)}
                  className={styles.select}
                >
                  <option value="todos">Todos</option>
                  <option value="conStock">Con stock</option>
                  <option value="sinStock">Sin stock</option>
                </select>
              </div>

              <div className={styles.filterGroup}>
                <label>Descuento:</label>
                <select
                  value={discountFilter}
                  onChange={(e) => setDiscountFilter(e.target.value)}
                  className={styles.select}
                >
                  <option value="todos">Todos</option>
                  <option value="conDescuento">Con descuento</option>
                  <option value="sinDescuento">Sin descuento</option>
                </select>
              </div>

              <div className={styles.filterGroup}>
                <label>Talle:</label>
                <select
                  value={sizeFilter}
                  onChange={(e) => setSizeFilter(e.target.value)}
                  className={styles.select}
                >
                  <option value="todos">Todos los talles</option>
                  {availableSizes.map(({size, count}) => (
                    <option key={size} value={size}>
                      {size} ({count} disponible{count !== 1 ? 's' : ''})
                    </option>
                  ))}
                </select>
              </div>

              <button onClick={clearFilters} className={styles.clearFiltersButton}>
                Limpiar Filtros
              </button>
            </div>

            <div className={styles.resultsInfo}>
              <p>Mostrando {filteredAndSortedProducts.length} de {list.length} productos</p>
            </div>
          </div>

          <div className={styles.tableContainer}>
            <table className={styles.productsTable}>
              <thead>
                <tr>
                  <th>Imagen</th>
                  <th>Nombre</th>
                  <th>SKU</th>
                  <th>Categora</th>
                  <th>Precio</th>
                  <th>Stock</th>
                  <th>Talles (cant)</th>
                  <th>Descuento</th>
                  <th>Acciones</th>
                </tr>
              </thead>
              <tbody>
                {filteredAndSortedProducts.map((product) => (
                  <tr key={product.sku}>
                    <td>
                      {product.image ? (
                        <img 
                          src={product.image} 
                          alt={product.name} 
                          className={styles.productImage}
                        />
                      ) : (
                        <div className={styles.noImage}>Sin imagen</div>
                      )}
                    </td>
                    <td>
                      <div className={styles.productInfo}>
                        <strong>{product.name}</strong>
                        {product.description && (
                          <small>{product.description.substring(0, 50)}...</small>
                        )}
                      </div>
                    </td>
                    <td className={styles.skuProductList}>{product.sku}</td>
                    <td>
                      <span className={styles.categoryTag}>{product.category}</span>
                    </td>
                    <td>
                      <div className={styles.priceInfo}>
                        {Number(product.discount) > 0 && (
                          <span className={styles.originalPrice}>
                            {formatPrice(product.price)}
                          </span>
                        )}
                        <span className={styles.currentPrice}>
                          {Number(product.discount) > 0 
                            ? formatPrice(product.price * (1 - Number(product.discount) / 100))
                            : formatPrice(product.price)
                          }
                        </span>
                      </div>
                    </td>
                    <td>
                      <span className={`${styles.stockStatus} ${Number(product.stock) > 0 ? styles.inStock : styles.outOfStock}`}>
                        {product.stock}
                      </span>
                    </td>
                    <td className={styles.sizesCell}>
                      {renderSizes(product.sizes, product.talle)}
                    </td>
                    <td>
                      {Number(product.discount) > 0 ? (
                        <span className={styles.discountTag}>-{product.discount}%</span>
                      ) : (
                        <span>-</span>
                      )}
                    </td>
                    <td>
                      <div className={styles.actions}>
                        <button
                          onClick={() => handleEditProduct(product)}
                          className={styles.editButton}
                          title="Editar"
                        >
                          
                        </button>
                        <button
                          onClick={() => setConfirmDelete(product.sku)}
                          className={styles.deleteButton}
                          title="Eliminar"
                        >
                          
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {filteredAndSortedProducts.length === 0 && (
              <div className={styles.noResults}>
                <p>No se encontraron productos con los filtros aplicados.</p>
              </div>
            )}
          </div>

          {showForm && (
            <ProductForm
              product={editingProduct}
              mode={editingProduct ? "edit" : "create"}
              onClose={handleCloseForm}
            />
          )}

          {confirmDelete && (
            <div className={styles.confirmModal}>
              <div className={styles.confirmContent}>
                <h3>Confirmar Eliminacin</h3>
                <p>Ests seguro de que quieres eliminar este producto? Esta accin no se puede deshacer.</p>
                <div className={styles.confirmActions}>
                  <button
                    onClick={() => setConfirmDelete(null)}
                    className={styles.cancelButton}
                  >
                    Cancelar
                  </button>
                  <button
                    onClick={() => handleDeleteProduct(confirmDelete)}
                    className={styles.confirmDeleteButton}
                  >
                    Eliminar
                  </button>
                </div>
              </div>
            </div>
          )}
        </>
      )}

      {activeTab === 'invoicing' && (
        <InvoiceForm />
      )}

      {activeTab === 'sales' && (
        <SalesHistory />
      )}

    </div>
  );
};

export default AdminPanel;