import { createContext, useContext, useMemo, useState, useEffect } from "react";
import useProducts from "./useProducts.jsx";
const CartContext = createContext(null);

export const CartProvider = ({ children }) => {
  const { list } = useProducts();
    const [items, setItems] = useState(() => {
    try {
      const storedItems = window.localStorage.getItem('cartItems');
      return storedItems ? JSON.parse(storedItems) : [];
    } catch (error) {
      console.error('Error reading from localStorage', error);
      return [];
    }
  });
    const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    try {
      window.localStorage.setItem('cartItems', JSON.stringify(items));
    } catch (error) {
      console.error('Error writing to localStorage', error);
    }
  }, [items]);

  const findProduct = (sku) => list.find(p => p.sku === sku);
  const getFinalPrice = (product) => {
    if (!product) return 0;
    return Number(product.discount) > 0
      ? product.price * (1 - Number(product.discount) / 100)
      : product.price;
  };

  const openCart = () => setIsOpen(true);
  const closeCart = () => setIsOpen(false);

  const addToCart = (product, selections) => {
    if (!product || !selections || !selections.length) return;
    const existing = items.find(i => i.sku === product.sku);
    const currentMap = new Map((existing?.selections || []).map(s => [s.size, s.quantity]));
    for (const { size, quantity } of selections) {
      const avail = (product.sizes || []).find(s => s.size === size)?.quantity;
      const already = currentMap.get(size) || 0;
      if (avail !== undefined && already + quantity > avail) {
        throw new Error(`Stock insuficiente para talle ${size}. Disponible: ${avail - already} (Revisa tu carrito)`);
      }
    }
    setItems(prev => {
      const idx = prev.findIndex(i => i.sku === product.sku);
      if (idx === -1) {
        return [...prev, {
          sku: product.sku,
          name: product.name,
          image: product.image,
          price: product.price,
          discount: product.discount || 0,
          selections: selections.map(s => ({ size: s.size, quantity: s.quantity }))
        }];
      } else {
        const next = [...prev];
        const selMap = new Map(next[idx].selections.map(s => [s.size, s.quantity]));
        for (const { size, quantity } of selections) {
          selMap.set(size, (selMap.get(size) || 0) + quantity);
        }
        next[idx] = { ...next[idx], selections: Array.from(selMap, ([size, quantity]) => ({ size, quantity })) };
        return next;
      }
    });
    setIsOpen(true);
  };

  const removeFromCart = (sku) => {
    setItems(prev => prev.filter(i => i.sku !== sku));
  };

    const updateItemQuantity = (sku, size, delta) => {
    const product = findProduct(sku);
    if (!product) return;

    setItems(prev => {
      const next = [...prev];
      const itemIndex = next.findIndex(i => i.sku === sku);
      if (itemIndex === -1) return prev;

      const item = { ...next[itemIndex] };
      const selIndex = item.selections.findIndex(s => s.size === size);
      if (selIndex === -1) return prev;

      const newQuantity = item.selections[selIndex].quantity + delta;
      const stock = product.sizes.find(s => s.size === size)?.quantity ?? 0;

      if (newQuantity > stock) {
        console.warn(`Stock insuficiente para talle ${size}. Disponible: ${stock}`);
        return prev;
      }

      if (newQuantity <= 0) {
        item.selections = item.selections.filter(s => s.size !== size);
      } else {
        item.selections = item.selections.map(s => 
          s.size === size ? { ...s, quantity: newQuantity } : s
        );
      }

      if (item.selections.length === 0) {
        return next.filter(i => i.sku !== sku);
      } else {
        next[itemIndex] = item;
        return next;
      }
    });
  };

  const updateItemSelections = (sku, selections) => {
    const product = findProduct(sku);
    if (!product) return;
    for (const { size, quantity } of selections) {
      const avail = (product.sizes || []).find(s => s.size === size)?.quantity;
      if (avail !== undefined && quantity > avail) {
        throw new Error(`Stock insuficiente para talle ${size}. Disponible: ${avail}`);
      }
    }
    setItems(prev => prev.map(i => i.sku === sku ? { ...i, selections: selections.filter(s => s.quantity > 0) } : i));
  };

  const clearCart = () => setItems([]);

  const totalItems = useMemo(() => items.reduce((acc, i) => acc + i.selections.reduce((a, s) => a + s.quantity, 0), 0), [items]);
  const totalCost = useMemo(() => items.reduce((acc, i) => {
    const product = findProduct(i.sku);
    const unit = getFinalPrice(product ?? i);
    const qty = i.selections.reduce((a, s) => a + s.quantity, 0);
    return acc + unit * qty;
  }, 0), [items, list]);

  const buildWhatsAppMessage = () => {
    const lines = ["Hola, Qu tal? Quisiera hacer el siguiente pedido:"];
    for (const i of items) {
      const sels = i.selections.map(s => `${s.size} x ${s.quantity}`).join(", ");
      lines.push(`${i.name}: ${sels}`);
    }
    lines.push("Espero su respuesta!");
    return lines.join("\n");
  };

  return (
    <CartContext.Provider value={{
      items,
      isOpen,
      openCart,
      closeCart,
      addToCart,
      removeFromCart,
      updateItemSelections,
      updateItemQuantity,
      clearCart,
      totalItems,
      totalCost,
      buildWhatsAppMessage,
    }}>
      {children}
    </CartContext.Provider>
  );
};

const useCart = () => useContext(CartContext);
export default useCart;
