import React from 'react';
import style from './Pagination.module.css';

const Pagination = ({ productsPerPage, totalProducts, currentPage, paginate, totalPages }) => {
  const pageNumbers = [];
  const maxPagesToShow = 5;

  let startPage = Math.max(1, currentPage - Math.floor(maxPagesToShow / 2));
  let endPage = Math.min(totalPages, startPage + maxPagesToShow - 1);
  if (endPage - startPage + 1 < maxPagesToShow) {
    startPage = Math.max(1, endPage - maxPagesToShow + 1);
  }

  for (let i = startPage; i <= endPage; i++) {
    pageNumbers.push(i);
  }

  return (
    <nav className={style.pagination}>
      <ul className={style.paginationList}>
        <li className={`${style.pageItem} ${currentPage === 1 ? style.disabled : ''}`}>
          <button 
            onClick={() => paginate(1)} 
            className={style.pageLink}
            disabled={currentPage === 1}
          >
            
          </button>
        </li>
        <li className={`${style.pageItem} ${currentPage === 1 ? style.disabled : ''}`}>
          <button 
            onClick={() => paginate(currentPage - 1)} 
            className={style.pageLink}
            disabled={currentPage === 1}
          >
            
          </button>
        </li>
        
        {startPage > 1 && (
          <li className={style.pageItem}>
            <span className={style.pageDots}>...</span>
          </li>
        )}
        
        {pageNumbers.map(number => (
          <li key={number} className={`${style.pageItem} ${currentPage === number ? style.active : ''}`}>
            <button 
              onClick={() => paginate(number)} 
              className={style.pageLink}
            >
              {number}
            </button>
          </li>
        ))}
        
        {endPage < totalPages && (
          <li className={style.pageItem}>
            <span className={style.pageDots}>...</span>
          </li>
        )}
        
        <li className={`${style.pageItem} ${currentPage === totalPages ? style.disabled : ''}`}>
          <button 
            onClick={() => paginate(currentPage + 1)} 
            className={style.pageLink}
            disabled={currentPage === totalPages}
          >
            
          </button>
        </li>
        <li className={`${style.pageItem} ${currentPage === totalPages ? style.disabled : ''}`}>
          <button 
            onClick={() => paginate(totalPages)} 
            className={style.pageLink}
            disabled={currentPage === totalPages}
          >
            
          </button>
        </li>
      </ul>
    </nav>
  );
};

export default Pagination;
