import React from "react";
import style from "./styledCategories.module.css";
import useProducts from "../../../../context/useProducts.jsx";
import { useNavigate } from "react-router";
import categories from "../../../../data/categories";

const CategoriesSection = () => {
  const { setCategory, setSearch } = useProducts();
  const navigate = useNavigate();
  const onClick = (category) => {
    setCategory(category);
    setSearch("");
    navigate("/products");
  };
  return (
    <>
      <section id={style.categoriesSection}>
        <article>
          <h1>Marcas Disponibles</h1>
          <a onClick={() => onClick("todos")}>Ver todas</a>
        </article>
        <ul>
          {categories
            .filter((cat) => cat.name !== "todos")
            .map((cat) => (
              <li key={cat.name} onClick={() => onClick(cat.name)}>
                <figure>
                  <img src={cat.image} alt={cat.name} />
                </figure>
                <figcaption>{cat.name}</figcaption>
              </li>
            ))}
        </ul>
      </section>
    </>
  );
};

export default CategoriesSection;
